@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

// fold default
.fold_box_wrap{
	.fold_box_list {
		.fold_box{
			+ .fold_box {
				margin-top: 10px;
			}
		}
	}

	.fold_box {
		background: variables.$colorGray06;
		border: 1px solid variables.$colorGray04;
		border-radius: 10px;
		box-sizing: border-box;

		.fold_box_header, .fold_box_contents {
			padding: 0 20px;
		}

		.fold_box_header {
			position: relative;
			padding: 26px 30px 22px 19px;
			cursor: pointer;
			@include mixins.font_size_sm;
			font-weight: variables.$fontWeightBold;

			&:after {
				content: '';
				position: absolute;
				display: block;
				width: 10px;
				height: 5px;
				right: 20px;
				top: 50%;
				@include mixins.ratio_images('../images/arw_fold_toggle_mok', 'png', 10px, 5px);
				@include mixins.transform(translateY(-50%));
			}

			.btn_fold{
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
			}
		}

		.fold_box_contents {
			height: 0;
			overflow: hidden;
		}

		&.expanded {
			background: #fff;
			border-color: variables.$textColorBlack;

			.fold_box_header {
				&:after{
					@include mixins.transform(translateY(-50%) rotate(180deg));
				}
			}

			> .fold_box_contents {
				height: auto;
				padding: {
					top: 4px;
					bottom: 23px;
				}

				.form_wrap{
					+ .btn_wrap{
						margin-top: 20px;
					}
				}
			}
		}

		&.no_fold{
			.fold_box_header{
				&:after{
					content: none;
				}

				cursor: default;
			}
		}
	}
}