@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

.toast_wrap {
	display: none;
	position: fixed;
	left: 50%;
	bottom: 36px; // 하단 doc만큼
	width: calc(100% - 40px);
	max-width: 335px;
	z-index: 1005;
	@include mixins.transform(translateX(-50%));
	@include mixins.transition(bottom .23s);
	font-size: 0;

	.toast_message {
		@include mixins.justify_width(center, center, row, true);
		box-sizing: border-box;
		width: 100%;
		min-height: 50px;
		padding: 15px 30px 11px;
		border-radius: 10px;
		background: rgba(variables.$kyoboPurple, 0.9);
		border: 1px solid variables.$kyoboPurple;
		box-shadow: 0 5px 10px rgba(variables.$textColorBlack, 0.16);
		color: #fff;
		@include mixins.font_size_sm;
		font-weight: variables.$fontWeightMedium;
		text-align: center;
	}
}

.dialog_wrapper {
	.ui-dialog {
		&.dialog_bottom {
			// 메인팝업
			&.main {
				.dialog_wrap {
					.dialog_contents {
						position: relative;
						margin: 0;

						.swiper-container {
							.swiper-slide {

							}
						}

						.swiper-pagination {
							top: 20px;
							right: 20px;
							bottom: auto;
							left: auto;
							transform: translateX(0);
						}

						.main_pop_link {
							display: block;

							img {
								width: 100%;
							}
						}
					}

					.dialog_footer {
						margin: 0;
						justify-content: flex-start;
						-webkit-justify-content: flex-start;

						.btn_main_pop {
							flex: 0 1 auto;
							padding-top: 1px;
							height: 54px;
							text-align: left;
							@include mixins.font_size_sm;
							color: variables.$textColorLightGray;
							font-weight: variables.$fontWeightMedium;

							&.close {
								margin-left: auto;
								text-align: right;
								color: variables.$textColorBlack;
								font-weight: variables.$fontWeightBold;
							}
						}
					}
				}
			}

			// 공유하기 팝업
			&.sns_share {
				.dialog_wrap {
					.dialog_contents {
						.sns_share_list {
							font-size: 0;

							.list_item {
								display: inline-block;;
								vertical-align: top;
								width: calc(100% / 4);

								&.list_item:nth-child(n+5) {
									margin-top: 20px;
								}

								.btn_sns_share {
									display: block;
									text-align: center;

									.ico_sns {
										display: block;
										width: 53px;
										height: 53px;
										margin: 0 auto;

										&.kakao {
											@include mixins.ratio_images('../images/ico_kakao_mok', 'png', 53px, 53px);
										}

										&.kakako_story {
											@include mixins.ratio_images('../images/ico_kakao_story_mok', 'png', 53px, 53px);
										}

										&.line {
											@include mixins.ratio_images('../images/ico_line_mok', 'png', 53px, 53px);
										}

										&.band {
											@include mixins.ratio_images('../images/ico_band_mok', 'png', 53px, 53px);
										}

										&.facebook {
											@include mixins.ratio_images('../images/ico_facebook_mok', 'png', 53px, 53px);
										}

										&.twitter {
											@include mixins.ratio_images('../images/ico_twitter_mok', 'png', 53px, 53px);
										}

										&.message {
											@include mixins.ratio_images('../images/ico_message_mok', 'png', 53px, 53px);
										}

										&.more {
											@include mixins.ratio_images('../images/ico_more_mok', 'png', 53px, 53px);
										}
									}

									.sns_text {
										display: block;
										margin-top: 6px;
										@include mixins.font_size_xs;
										color: variables.$textColorBlack;
									}
								}

							}
						}
					}

					.dialog_footer {
						margin: 20px 20px 30px;
						padding: 0;

						.sns_share_link_box {
							@include mixins.justify_width(center);
							width: 100%;
							height: 50px;
							border: 1px solid variables.$colorGray02;
							border-radius: 6px;
							line-height: 48px;
							font-size: 0;
							box-sizing: border-box;

							.sns_share_link {
								flex: 2;
								height: 100%;
								padding: 0 46px 0 12px;
								@include mixins.font_size_sm;
								line-height: 49px;
								color: variables.$textColorLightGray;
								box-sizing: border-box;
								overflow: hidden;
							}

							.btn_share_link {
								flex: 1;
								height: 100%;
								@include mixins.font_size_sm;
								line-height: 49px;
								color: variables.$textColorBlack;
								border-left: 1px solid variables.$colorGray02;
								border-radius: 0;
								text-align: center;
								box-sizing: border-box;
							}
						}
					}
				}
			}
		}

		&.dialog_full {
			.dialog_wrap {
				// 이미지 크게보기
				&.img_zoom {
					.dialog_contents {
						margin: 0;
						padding-top: 97px;

						.gallery_top_wrap {
							height: calc(var(--reVh, 1vh) - 115px - 97px);

							.gallery_top {
								height: 100%;
								padding-bottom: 66px;
								box-sizing: border-box;

								.swiper-wrapper {
									.swiper-slide {
										width: 100%;
									}
								}

								.swiper-pagination {
									bottom: 14px;
								}
							}
						}

						.gallery_thumbs_wrap {
							height: 115px;
							padding: 15px 0 30px;
							border-top: 1px solid variables.$colorGray03;
							background-color: variables.$colorGray06;
							box-sizing: border-box;

							.gallery_thumbs {
								padding-right: 40px;

								.swiper-wrapper {
									padding: 0 20px;

									&.align_center {
										justify-content: center;
									}

									.swiper-slide {
										width: 70px;
										height: 70px;
										border-radius: 6px;
										overflow-y: hidden;
										margin-right: 10px;
										box-sizing: border-box;

										&:last-child {
											margin-right: 0;
										}

										.btn_thumbs {
											width: 100%;
											height: 100%;

											.thumb_img_box {
												position: relative;
												display: block;
												width: 100%;
												padding-top: 100%;
												background-size: cover;
												background-repeat: no-repeat;
												background-position: center;
											}

											img {
												display: none;
											}
										}
									}

									.swiper-slide-thumb-active {
										&:before {
											content: '';
											position: absolute;
											top: 0;
											left: 0;
											width: 100%;
											height: 100%;
											border: 2px solid variables.$textColorBlack;
											box-sizing: border-box;
											border-radius: 8px;
											z-index: 2;
										}
									}
								}
							}

						}
					}
				}

				// 미리보기 팝업
				&.preview {
					.btn_dialog_close, .preview_header, .preview_footer {
						position: fixed;
						@include mixins.transition(transform .23s);
					}

					.btn_dialog_close {
						z-index: 2;
					}

					.dialog_contents {
						margin: 0;
						padding-bottom: 74px;

						.preview_header {
							height: 36px;
							padding: {
								top: 12px;
								bottom: 12px;
							}
							background: #fff;
							box-sizing: content-box;
							left: 0;
							right: 0;
							top: 0;
							z-index: 1;

							&.tab_list_wrap {
								text-align: center;

								.tabs {
									display: inline-block;
									margin: 0;

									.tab_item {
										min-width: 80px;
										height: 36px;
										padding: 0 4px;

										.tab_link {
											background: #fff;
											border-color: #d5d5d5;

											.tab_text {
												line-height: 34px;
												font-weight: variables.$fontWeightMedium;
											}
										}

										&.ui-state-active {
											.tab_link {
												background: #5055b1;
												border-color: #5055b1;
											}
										}
									}
								}
							}
						}

						.preview_body {
							.preview_area {
								.preview_item {
									img {
										width: 100%;
									}
								}
							}

							.preview_menu {
								position: fixed;
								left: 0;
								top: 0;
								right: 0;
								bottom: 0;
								z-index: 3;

								.ui-widget-overlay {
									z-index: auto;
								}

								.preview_menu_inner {
									position: relative;
									height: calc(100% - 80px);
									background: #fff;
									margin-left: 75px;
									padding: 60px 20px 20px 30px;
									overflow-y: scroll;
									@include mixins.justify_width(start, start, column);

									@include mixins.respond-to(mobileMin) {
										margin-left: 0;
									}

									.thumbnail_product_item {
										width: 100%;
										flex: 0 0 94px;
										min-width: auto;
										margin-bottom: 16px;
									}

									.btn_wrap {
										width: 100%;
									}

									.title_wrap {
										width: 100%;
										margin-top: 40px;
										padding-bottom: 13px;
									}

									.preview_index_box {
										width: calc(100% - 40px);
										flex: 1 1 100%;
										background: #f7f7f7;
										border-radius: 10px;
										padding: 20px;
										overflow: scroll;

										@media (orientation: landscape) {
											flex: 0 0 170px;
											overflow: auto;
										}
									}

									.sns_share_box {
										width: calc(100% - 36px);
										flex: 1 0 32px;
										max-width: 224px;
										margin: 24px auto 0;
										padding: 0 18px;
										text-align: center;
										@include mixins.justify_width(end, between);

										.btn_sns_share {
											width: 32px;
											height: 32px;
											background: {
												position: center;
												repeat: no-repeat;
											}

											&.kakao {
												@include mixins.ratio_images('../images/btn_kakao_share_sm', 'png', 32px, 32px);
											}

											&.band {
												@include mixins.ratio_images('../images/btn_band_share_sm', 'png', 32px, 32px);
											}

											&.facebook {
												@include mixins.ratio_images('../images/btn_facebook_share_sm', 'png', 32px, 32px);
											}

											&.twitter {
												@include mixins.ratio_images('../images/btn_twitter_share_sm', 'png', 32px, 32px);
											}

											&.url_copy {
												@include mixins.ratio_images('../images/btn_copy_share_sm', 'png', 32px, 32px);
											}
										}
									}

									.preview_help_box {
										width: 100%;
										margin-top: 24px;
										text-align: center;

										.help_title {
											display: inline-block;
											color: #797979;
											@include mixins.font_size_sm;
											font-weight: variables.$fontWeightMedium;
										}

										.btn_tooltip_sm {
											margin: 3px 0 0 4px;
										}
									}
								}

								&:not(.active) {
									display: none;
								}

								.btn_menu_close .ico_menu_close {
									position: relative;
									z-index: 3;
									display: block;
									width: 24px;
									height: 24px;
									background-image: url(../images/btn_dialog_close_mok.png);
									background-size: 24px 24px;
								}
							}
							.preview_footer {
								@include mixins.justify_width(center, end);
								height: 33px;
								margin: 0;
								padding: 16px 20px 24px;
								background: #fff;
								border-top: 1px solid #999;
								left: 0;
								right: 0;
								bottom: 0;

								.slider_wrap {
									flex: 1;

									.page_slider {
										margin: 15px;
									}
								}

								.btn_preview_menu {
									margin-left: 20px;
									flex: 0 0 24px;

									.ico_preview_menu {
										display: inline-block;
										width: 24px;
										height: 24px;
										background: {
											position: center;
											repeat: no-repeat;
										}
										@include mixins.ratio_images('../images/btn_preview_menu', 'png', 24px, 24px);
									}
								}
							}
						}
					}


					&:not(.show_header_footer) {
						.btn_dialog_close, .preview_header {
							transform: translateY(-60px);
						}

						.preview_footer {
							transform: translateY(74px);
						}
					}
				}

				&.dialog_history {
					.history_list_wrap{

						.history_list{
							.history_item{
								position: relative;
								overflow: hidden;
								@include mixins.justify_width(end);
								width: 100%;
								padding: 17px;
								border: 1px solid variables.$colorGray04;
								border-radius: 10px;
								box-sizing: border-box;

								.prod_area{
									margin-right: 8px;

									.prod_info_box{
										.prod_price{
											margin-top: 12px;
										}
									}
								}

								.history_control_box{
									flex: {
										shrink: 0;
										basis: 30px;
									}

									margin-left: auto;

									.btn_wish{
										border-radius: 15px;
									}

									.btn_history_delete{
										position: absolute;
										width: 20px;
										height: 20px;
										padding: 2px;
										top: 16px;
										right: 16px;

										.ico_history_del{
											width: 16px;
											height: 16px;
											@include mixins.ratio_images('../images/btn_history_del_mok', 'png', 16px, 16px);
										}
									}
								}

								~ .history_item{
									margin-top: 10px;
								}
							}
						}
					}
				}
			}

			// 우편번호 찾기 팝업
			&.address {
				.dialog_wrap {

					.dialog_contents {
						@include mixins.justify_width(start, start, column);

						.form_desc {
							margin-top: 7px;
						}

						.address_tip {
							width: 100%;
							padding: 20px;
							margin-top: 17px;
							background-color: variables.$colorGray06;
							border-radius: 10px;
							box-sizing: border-box;

							.tip {
								margin-top: 0;
								@include mixins.font_size_sm;
								color: variables.$textColorBlack;
								text-indent: 33px;
								padding-left: 0;

							}

							.address_tip_list {
								margin-top: 8px;

								.address_tip_item {
									position: relative;
									padding-left: 9px;
									@include mixins.font_size_xs;
									color: variables.$textColorGray;

									&.address_tip_item + .address_tip_item {
										margin-top: 6px
									}

									&:before {
										content: '';
										position: absolute;
										top: 50%;
										transform: translateY(-50%);
										left: 0;
										width: 3px;
										height: 3px;
										border-radius: 50%;
										background-color: variables.$colorGray01;
									}
								}
							}
						}

						// 회원 기존 배송지 영역
						.member_address_wrap {
							display: block;
							width: 100%;
							box-sizing: border-box;
							margin-bottom: 29px;

							.member_address_inner {
								@include mixins.justify_width(center);
								width: 100%;
								padding: 19px 19px 16px;
								background: #fff;
								border: 1px solid variables.$colorGray02;
								border-radius: 10px;
								box-sizing: border-box;

								.member_address {
									flex: 1;

									.member_name {
										display: block;
										color: variables.$textColorDarkGray;
										@include mixins.font_size_def;
										font-weight: variables.$fontWeightMedium;

										.badge_md {
											margin-right: 6px;
											vertical-align: 1px;
										}
									}

									.member_address {
										display: block;
										margin-top: 10px;
										color: variables.$textColorDarkGray;
										@include mixins.font_size_sm;
									}
								}

								.btn_wrap {
									margin-left: 15px;
								}
							}

							.bul_item_asterisk {
								margin-top: 11px;
							}
						}

						/*주소찾기 팝업(검색결과 있음)*/
						.address_search_total {
							margin-top: 18px;
							border-bottom: 1px solid variables.$textColorBlack;
							padding-bottom: 12px;

							.address_search_total_text {
								@include mixins.font_size_def;
								color: variables.$textColorBlack;
								font-weight: variables.$fontWeightBold;

								.search_value {
									color: variables.$textColorPurple;
								}
							}

							.address_select_area {
								@include mixins.justify_width;
								margin-top: 12px;

								.form_sel {
									flex: 1;

									~ .form_sel {
										margin-left: 5px;
									}
								}
							}

							.info_text {
								margin-top: 6px;
							}
						}

						.address_search_total + .address_success_result {

						}

						.address_success_result {
							.address_success_wrap {
								.address_success_list {
									.address_success_item {
										@include mixins.font_size_sm;
										flex-wrap: wrap;
										padding: 12px 0 14px 0;
										border-bottom: 1px solid variables.$colorGray04;

										.success_item_post {
											@include mixins.font_size_sm;
											color: variables.$textColorBlack;
											font-weight: variables.$fontWeightMedium;
										}

										.success_item_text {
											margin-top: 4px;

											.road_name_wrap,
											.land_number_wrap {
												@include mixins.justify_width;

												.badge_md {
													width: 44px;
													height: 22px;
													padding: 2px 0 3px;
													@include mixins.font_size_xxs;
												}

												.btn_road_name,
												.btn_land_number {
													width: calc(100% - 50px);
													margin-left: 6px;
													@include mixins.font_size_sm;
													color: variables.$textColorBlack;
													text-align: left;

													&:hover {
														text-decoration: underline;
													}
												}

												.btn_land_number {
													color: variables.$textColorGray;
												}

											}

											.road_name_wrap {

											}

											.land_number_wrap {
												margin-top: 5px;
											}
										}

										.success_detail {
											@include mixins.justify_width;
											width: 100%;
											margin-top: 12px;
											display: none;

											&.active {
												@include mixins.justify_width;
											}

											input[type=text] {

											}

											input[type=text] + [class^=btn_] {
												margin-left: 6px;
											}
										}
									}
								}

							}
						}

						.address_success_result + .btn_wrap {
							margin-top: 20px;
							margin-bottom: 40px;
						}

						/*주소찾기 팝업(검색결과 없음)*/
						.search_fail {
							padding: 20px;
							background-color: variables.$colorGray06;
							color: variables.$textColorGray;
							@include mixins.font_size_xs;
							box-sizing: border-box;
							border-radius: 10px;
							margin-top: 17px;

							.text_search_fail {
								color: variables.$textColorBlack;
								@include mixins.font_size_sm;
								font-weight: variables.$fontWeightBold;
								margin-bottom: 8px;

								.fail_search_value {
									color: variables.$textColorPurple;
								}
							}
						}

						.address_contents_inner {
							@include mixins.justify_width(start, start, column);
							align-items: stretch;
							flex: 1;
							width: 100%;

							.bul_item_dot {
								margin-top: auto;
							}
						}
					}

					// 비로그인
					&.has_btn {
						.dialog_contents {
							margin-bottom: 20px;
						}

						.dialog_footer {
							margin-top: 0;
							padding-top: 18px;
							border-top: 1px solid variables.$colorGray04;

							.login_info_text {
								flex: 1;
								@include mixins.font_size_sm;
								color: variables.$textColorBlack;
								font-weight: variables.$fontWeightMedium;
							}

							[class^=btn_] {
								flex: 0 auto;
								margin-left: 25px;
							}
						}
					}
				}
			}
		}

		.dialog_wrap {
			// 앱 종료 팝업
			&.app_off {
				.dialog_header {
					display: none;
				}

				.dialog_contents {
					margin: 0;
				}

				.dialog_footer {
					margin-top: 0;
				}
			}

			// 마케팅 수신동의 유도 팝업
			&.marketing_agree {
				.dialog_header {
					display: none;
				}

				.dialog_contents {
					margin: 20px;

					.form_chk {
						margin-top: 10px;
					}
				}

				.dialog_footer {
					margin-top: 0;
				}
			}

			// 리뷰 작성
			&.product_review {
				.dialog_contents {
					opacity: 0;

					.review_prod_info {
						@include mixins.justify_width(start, start, column);
						height: 100%;

						.thumbnail_round_box {
							width: 100%;
							@include mixins.justify_width(center, center);
							flex-basis: 100%;
							position: relative;
							border-color: #fff;
							border-radius: 10px;
							padding: 0;
							text-align: center;
							box-sizing: border-box;
							@include mixins.transition(padding .23s, border-color .23s);

							.thumbnail_product_item {
								&:not(.horizontal_type) {
									.thumbnail_area {
										width: auto;
										margin-bottom: 30px;
										text-align: center;
									}

									.info_area {
										padding: 0;

										.rating-container {
											&.has_caption {
												margin-top: 30px;

												.caption {
													display: none;
												}
											}
										}
									}
								}

								&.horizontal_type {
									.thumbnail_area {
										width: 64px;
									}
								}
							}

						}

						.info_text_box {
							width: 100%;
						}
					}

					.form_wrap {
						.form_box {
							display: none;
						}
					}
				}

				.dialog_footer {
					display: none;
				}

				&.open {
					.dialog_contents {
						animation: reviewContFadeInOut .7s forwards;
					}
				}

				&.review_next {
					.dialog_contents {
						.review_prod_info {
							height: auto;

							.thumbnail_round_box {
								justify-content: flex-start;
								flex-basis: auto;
								border-color: variables.$colorGray04;
								padding: 16px;
								text-align: left;

								.thumbnail_product_item {
									-webkit-box-align: start;
									-moz-box-align: start;
									-ms-flex-align: start;
									-webkit-align-items: flex-start;
									align-items: flex-start;
								}
							}
						}

						.form_wrap {
							margin-top: 24px;

							.form_box {
								&.animated {
									display: block;
									animation: reviewContFadeInOut .7s forwards;
								}

								.file_attach_val {
									margin-top: 6px;
								}

								.file_list_box {
									@include mixins.scrollX;

									.file_list {
										white-space: nowrap;
									}
								}
							}
						}
					}

					.dialog_footer {
						@include mixins.justify_width(center, center);
						animation: reviewContFadeInOut .5s forwards;
					}
				}

				@keyframes reviewContFadeInOut {
					0% {
						opacity: 0;
						@include mixins.transform(translateY(10px));
					}

					100% {
						opacity: 1;
						@include mixins.transform(translateY(0));
					}
				}
			}

			//결합상품 저자 모두보기
			&.dialog_author {
				.number_text_list {
					.number_text_item {
						@include mixins.font_size_def;
						color: variables.$textColorBlack;

						.number_text {
							font-weight: variables.$fontWeightMedium;
							@include mixins.ellipsis();

							& + .round_gray_box {
								margin-top: 13px;
							}
						}

						.round_gray_box {
							.title_wrap {
								.title_heading {
									font-weight: variables.$fontWeightMedium;
								}
							}
						}

						& + .number_text_item {
							margin-top: 30px;
						}
					}
				}
			}

			// 매장별 재고 및 위치 팝업
			&.dialog_stock {
				.dialog_contents {
				}
			}

			//상품 상세 팝업
			.dialog_contents {
				.btn_author_link {
					position: relative;
					color: variables.$textColorGray;
					padding-right: 3px;

					&:after {
						position: absolute;
						right: 0;
						bottom: 0;
						content: ',';
						display: inline-block;
					}

					&:last-child {
						&:after {
							display: none;
						}
					}
				}

				.info_text {
					& + .bul_list {
						margin-top: 8px;
					}

					& + .round_gray_box {
						margin-top: 13px;
					}

					& + .title_wrap {
						margin-top: 16px;
					}

				}

				.bul_list {
					& + .btn_more_view {
						margin-top: 10px;
					}

					& + .info_text_box {
						margin-top: 10px;
					}

					& + .tbl_col_wrap {
						margin-top: 10px;
					}

					& + .bul_list {
						margin-top: 10px;
					}

					& + .title_wrap {
						margin-top: 13px;

						&.title_size_def {
							margin-top: 18px;
						}
					}
				}

				.toggle_textarea_list {
					.chk_col_item {
						.form_rdo {
							& + .byte_check_wrap {
								margin-top: 10px;
							}
						}
					}
				}

				.round_gray_box {
					& + .bul_list {
						margin-top: 12px;
					}

					& + .bul_item_asterisk {
						margin-top: 10px;
					}

					& + .title_wrap {
						margin-top: 20px;
					}
				}

				.info_text_box {
					& + .form_wrap {
						&.type_md {
							margin-top: 24px;
						}
					}
				}

				.tbl_col_wrap {
					& + .title_wrap {
						margin-top: 20px;
					}

					& + .bul_list {
						margin-top: 20px;
					}

					& + .bul_item_asterisk {
						margin-top: 11px;
					}
				}

				.bul_item_asterisk {
					& + .title_wrap {
						margin-top: 20px;
					}
				}

				// 타이틀 전체보기 팝업 내
				.prod_detail_title_wrap {
					display: block;
					position: relative;
					text-align: left;
					word-break: break-all;

					// 타이틀
					.prod_title_area {
						display: block;
						position: relative;

						.badge_list_box {
							margin-bottom: 15px;
						}

						.prod_title_box {
							display: block;
							position: relative;
							font-size: 0;

							.prod_label {
								display: inline;
								position: relative;
								padding-right: 4px;
								color: variables.$textColorPurple;
								@include mixins.font_size_md;
								font-weight: variables.$fontWeightRegular;

								& ~ .prod_label {
									padding-left: 5px;

									&:before {
										content: '·';
										display: block;
										position: absolute;
										left: -1px;
										top: 2px;
										font-size: variables.$fontSizeMd;
										line-height: 1;
										font-weight: variables.$fontWeightRegular;
										letter-spacing: variables.$letterSpacing;
									}
								}
							}

							.prod_alias {
								display: inline;
								position: relative;
								padding-right: 4px;
								color: variables.$textColorBlack;
								@include mixins.font_size_md;
								font-weight: variables.$fontWeightMedium;
							}

							.gap {
								visibility: hidden;
								position: absolute;
							}
						}

						.prod_title {
							display: inline;
							color: variables.$textColorBlack;
							@include mixins.font_size_md;
							font-weight: variables.$fontWeightBold;
						}
					}

					// 부제명 1,2, MD 부제
					.prod_desc_area {
						display: block;
						margin-top: 8px;

						.prod_desc_box {
							display: block;
							position: relative;
							font-size: 0;

							.prod_desc {
								display: inline;
								color: variables.$textColorGray;
								@include mixins.font_size_def;
								font-weight: variables.$fontWeightMedium;

								&.normal {
									color: variables.$textColorLightGray;
									font-weight: variables.$fontWeightRegular;
								}
							}

							.gap {
								display: inline-block;
								width: 1px;
								height: 10px;
								margin: 5px 10px 0;
								background-color: variables.$colorGray02;
								font-size: 0;
								vertical-align: top;
							}

							& ~ .prod_desc_box {
								margin-top: 7px;
							}
						}
					}
				}

				// 상품리뷰 팝업 내
				.all_review_wrap {
					.all_review_list {
						.all_review_item {
							padding: 0;
							border-bottom: none;
							box-sizing: border-box;

							.review_middle {
								.review_middle_box {
									.info_text {
										margin-bottom: 13px;
										color: variables.$textColorBlack;
									}

									.review_swiper {
										margin-top: 16px;

										.swiper-container {
											.swiper-wrapper {
												.swiper-slide {
													max-width: 335px;
													margin-right: 12px;

													img {
														width: 100%;
													}
												}
											}
										}
									}
								}
							}

							.review_button_common {
								margin-top: 13px;
							}

							.review_reply {
								border-top: 1px solid variables.$colorGray04;
								padding-top: 10px;
								margin-top: 20px;

								.byte_check_wrap {
									display: none;

									&.active {
										display: block;
									}

									.form_textarea {
										height: 80px;
									}
								}

								.reply_list {
									.reply_list_item {
										position: relative;
										padding: 20px 20px 24px 37px;
										box-sizing: border-box;
										background-color: variables.$colorGray06;
										margin-top: 8px;
										border-radius: 10px;

										.review_user_info {

											&:before {
												content: '';
												display: inline-block;
												width: 8px;
												height: 9px;
												@include mixins.ratio_images('../images/arw_reply_mok', 'png', 8px, 9px);
												position: absolute;
												top: 24px;
												left: 20px;
											}
										}

										.info_text {
											color: variables.$textColorBlack;
										}
									}
								}

								.btn_more_body {
									margin-top: 12px;
								}
							}
						}
					}

					.review_more_btn_wrap {
						margin-top: 24px;
						text-align: center;
					}
				}
			}
		}
	}
}

