@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

.portrait_img_box{
	position: relative;
	display: block;
	width: 100%;
	height: 100%;
	overflow: hidden;

	img{
		position: absolute;
		display: block;
		margin: 0 auto;
		object-fit: contain;
		max-width: 100%;
		max-height: 100%;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}
}

// contents gray box
.round_gray_box{
	display: block;
	padding: 24px 24px 30px;
	border-radius: 16px;
	background-color: variables.$colorGray06;
	box-sizing: border-box;

	.bottom_btn_area{
		margin: 30px 0 -6px;
		text-align: center;
	}

	.explan_text_list{
		.explan_title{
			display: block;
			margin-bottom: 7px;
			@include mixins.font_size_xs;
			color: variables.$textColorBlack;
			font-weight: variables.$fontWeightMedium;
		}

		.explan_desc{
			display: block;
			@include mixins.font_size_xs;
			color: variables.$textColorLightGray;

			~ .explan_title{
				border-top: 1px solid variables.$colorGray04;
				margin-top: 16px;
				padding-top: 14px;
			}
		}
	}

	+ .round_gray_box {
		margin-top: 20px;
	}

	&.type_sub{
		padding: 20px;
	}

	&.type_divide{
		padding: 0;
		background-color: #fff;
		border: 1px solid variables.$colorGray04;

		.box_header{
			border-radius: 16px 16px 0 0;
			padding: 19px 23px;
			border-bottom: 1px solid variables.$colorGray04;
			background-color: variables.$colorGray06;

			.right_box{
				margin-left: auto;
				font-size: 0;
			}

			&.type_sm{
				@include mixins.justify_width(center);
				padding: 10px 14px;
			}
		}

		.box_body{
			border-radius: 0 0 16px 16px;
			padding: 23px 23px;
		}
	}
}

/* auto overflow 공통 */
.auto_overflow_wrap {
	.auto_overflow_contents {
		overflow: hidden;
	}

	.auto_overflow_footer {
		display: none;
	}

	&.overflow {
		.auto_overflow_footer {
			display: block;
			margin-top: 12px;

			&.type_cont{
				margin-top: 24px;
				text-align: center;
			}
		}
	}

	&.active {
		> .auto_overflow_contents {
			max-height: none !important;
		}

		&.type_tbl_col{
			.auto_overflow_contents{
				&:after{
					content: none;
				}
			}
		}
	}

	&.type_tbl_col{
		.auto_overflow_contents{
			position: relative;
			border-bottom: 1px solid variables.$colorGray04;

			&:after{
				content: '';
				position: absolute;
				display: block;
				bottom: 0;
				left: 0;
				width: 100%;
				height: 10px;
				background-color: #fff;
			}
		}

		.tbl_col_wrap{
			border-bottom: none;
		}
	}

	&.type_text{
		&.overflow{
			.auto_overflow_contents {
				position: relative;

				&:after {
					content: '';
					width: 100%;
					height: 15px;
					background: linear-gradient(180deg, rgba(#fff, 0), rgba(#fff, 1) 15px);
					position: absolute;
					bottom: 0;
				}
			}

			&.active{
				.auto_overflow_contents {
					&:after {
						content: none;
					}
				}
			}
		}
	}
}

/* 상품/리뷰 컨텐츠 공통 사용 */
// " 좋았어요 텍스트
.review_quotes_text {
	display: inline-block;
	position: relative;
	padding-left: 18px;
	color: variables.$textColorPurple;
	@include mixins.font_size_xs;
	font-weight: variables.$fontWeightMedium;
	vertical-align: top;

	&:before {
		content: '';
		position: absolute;
		left: 0;
		top: 2px;
		width: 13px;
		height: 13px;
		@include mixins.ratio_images('../images/ico_quotes_mok', 'png', 13px, 13px);
	}
}

// 클로버 아이콘 + 점수 텍스트
.review_klover_text {
	display: inline-block;
	position: relative;
	padding-left: 19px;
	color: variables.$textColorBlack;
	@include mixins.font_size_xs;
	font-weight: variables.$fontWeightBold;
	vertical-align: top;

	&:before {
		content: '';
		position: absolute;
		left: 0;
		top: 4px;
		width: 13px;
		height: 13px;
		@include mixins.ratio_images('../images/ico_klover_sm_mok', 'png', 13px, 13px);
	}
}

// 클로버 아이콘 + 점수 텍스트 / 리뷰 갯수 텍스트
.review_klover_box {
	display: inline-block;
	font-size: 0;
	vertical-align: top;

	.review_desc {
		display: inline-block;
		margin: 1px 0 0 4px;
		color: variables.$textColorLightGray;
		@include mixins.font_size_xxs;
		vertical-align: top;
	}
}

.review_summary_wrap {
	display: block;
	font-size: 0;

	.gap {
		display: inline-block;
		width: 4px;
		height: 8px;
		margin: 5px 6px 0;
		@include mixins.ratio_images('../images/ico_slash_mok', 'png', 4px, 8px);
		vertical-align: top;
	}

	.review_quotes_text {
		margin-right: 1px;

		& + .gap {
			margin: 6px 6px 0;
		}
	}

	.rating-container {
		&.rating-xs {
			margin-top: 3px;
		}
	}
}

/* 할인쿠폰 */
.coupon_item {
	@include mixins.justify_width();
	align-items: stretch;
	position: relative;
	overflow: hidden;
	width: 100%;
	max-width: 335px;
	min-height: 153px;
	background: #fff;
	box-sizing: border-box;
	font-size: 0;

	&:before,
	&:after {
		content: '';
		position: absolute;
		right: 41px;
		z-index: 1;
		width: 17px;
		height: 7px;
		background: {
			repeat: no-repeat;
			position: center;
		}
	}

	&:before {
		top: -1px;
		@include mixins.ratio_images('../images/ico_coupon_dent_top_mok', 'png', 17px, 7px);
	}

	&:after {
		bottom: -1px;
		@include mixins.ratio_images('../images/ico_coupon_dent_bottom_mok', 'png', 17px, 7px);
	}

	.left_box {
		@include mixins.justify_width(start, center, column);
		flex: 1;
		flex-wrap: wrap;
		overflow: hidden;
		padding: 23px 25px 17px;
		background: #fff;
		border: 1px solid variables.$colorGray02;
		border-right: 0;
		border-radius: 10px 0 0 10px;
		box-sizing: border-box;

		.coupon_val {
			display: block;
			width: 100%;
			line-height: 1;
			word-break: break-all;

			.val {
				display: inline-block;
				color: variables.$textColorGreen;
				font-size: 34px;
				font-weight: variables.$fontWeightBold;
				vertical-align: top;

				.unit {
					color: variables.$textColorGreen;
					font-size: variables.$fontSizeMd;
					font-weight: variables.$fontWeightRegular;
					vertical-align: 2px;
				}
			}
		}

		.coupon_title {
			display: block;
			width: 100%;
			margin-top: 9px;
			color: variables.$textColorBlack;
			@include mixins.font_size_sm;
		}

		.coupon_info {
			display: block;
			width: 100%;
			margin-top: 8px;

			.coupon_info_list {
				.coupon_info_item {
					display: block;
					color: variables.$textColorLightGray;
					@include mixins.font_size_xs;
				}
			}
		}
	}

	.right_box {
		display: inline-block;
		position: relative;
		flex: 0 50px;
		overflow: hidden;
		background: variables.$colorGray05;
		border: 1px solid variables.$colorGray02;
		border-left: 0;
		border-radius: 0 10px 10px 0;
		box-sizing: border-box;

		&:before {
			content: '';
			display: block;
			position: absolute;
			left: 0;
			top: 0;
			width: 1px;
			height: calc(100% - 20px);
			margin: 10px 0;
			border-left: 1px dashed variables.$colorGray03;
			box-sizing: border-box;
		}

		.btn_coupon_down {
			display: inline-block;
			position: relative;
			width: 100%;
			height: 100%;
			background: {
				position: 15px center;
				repeat: no-repeat;
			}

			// 아이콘
			[class^=ico] {
				position: absolute;
				left: 50%;
				top: 50%;
				@include mixins.transform(translate(-50%, -50%));
			}

			.ico_down {
				width: 21px;
				height: 20px;
				background: {
					position: center;
					repeat: no-repeat;
				}
				@include mixins.ratio_images('../images/ico_coupon_down_mok', 'png', 21px, 20px);
			}

			.ico_down_done {
				width: 17px;
				height: 16px;
				background: {
					position: center;
					repeat: no-repeat;
				}
				@include mixins.ratio_images('../images/ico_coupon_down_disabled_mok', 'png', 17px, 16px);
			}

			&[disabled], &[aria-disabled], &.disabled {
				pointer-events: none;
				cursor: default;
			}
		}
	}

	&.disabled {
		opacity: 0.5;
	}
}
.coupon_list {
	.coupon_item {
		margin: 0 auto;

		& ~ .coupon_item {
			margin-top: 10px;
		}
	}

	& + .bul_list,
	& + [class^=bul_item_] {
		margin-top: 11px;
	}
}

.bubble_speech_box{
	position: relative;

	.bubble_speech_desc{
		position: absolute;
		right: 0;
		bottom: -38px;
		width: 225px;
		min-height: 30px;
		padding: 5px 7px;
		border-radius: 15px;
		background-color: #F7F7FB;
		border: 1px solid #B8BADF;
		@include mixins.font_size_xxs;
		color: variables.$textColorPurple;
		box-sizing: border-box;
		text-align: center;
		box-shadow: 0 5px 10px rgba(variables.$textColorBlack, 0.1);

		&:before{
			content: '';
			position: absolute;
			top: -7px;
			left: calc(100% - 43px);
			display: block;
			width: 10px;
			height: 7px;
			@include mixins.ratio_images('../images/bg_bubble_speech_mok', 'png', 10px, 7px);
		}
	}
}

/* 상품 */
.prod_area{
	.prod_thumb_box{
		.prod_link{
			display: block;
			height: 100%;
		}

		img{
			width: 100%;
		}

		&.type_cover{
			padding-top: 100%;

			background:{
				repeat: no-repeat;
				size: cover;
			}

			img{
				display: none;
			}
		}
	}

	.prod_info_box{
		margin-top: 10px;

		.prod_link {
			display: block;
			height: 100%;
		}

		.prod_name{
			font-weight: variables.$fontWeightMedium;
		}

		.prod_author{
			display: block;
			color: variables.$textColorGray;
		}

		.prod_price{
			display: block;
			font-size: 0;

			.percent{
				display: inline-block;
				margin-right: 5px;
				@include mixins.font_size_xs;
				font-weight: variables.$fontWeightBold;
				color: variables.$kyoboGreen;
				vertical-align: top;
			}

			.price{
				display: inline-block;
				font-size: 0;
				vertical-align: top;

				.val, .unit{
					@include mixins.font_size_xs;
				}

				.val{
					font-weight: variables.$fontWeightBold;
				}

				.unit{

				}
			}
		}

		.prod_period{
			display: block;
			margin-top: 5px;
			@include mixins.font_size_xxs;
			color: variables.$textColorGray;
		}
	}

	&:not(.horizontal) {
		.prod_info_box{
			margin-top: 10px;
		}
	}

	&.horizontal{
		overflow: hidden;
		width: 100%;
		@include mixins.justify_width(center);

		.prod_thumb_box{
			overflow: hidden;
			flex-shrink: 0;
			width: 84px;
			border-radius: 6px;
		}

		.prod_info_box{
			overflow: hidden;
			margin-left: 12px;

			.prod_link{
				.prod_name{
					@include mixins.font_size_xs;
					@include mixins.ellipsis(true, false);
				}

				.prod_author{
					margin-top: 5px;
					@include mixins.font_size_xxs;
				}

				.prod_price{
					margin-top: 20px;
				}

				.prod_period{
					margin-top: 5px;
				}
			}
		}
	}
}

/* 리뷰 */
.all_review_wrap {
	.all_review_list {
		.all_review_item {
			border-bottom: 1px solid variables.$colorGray04;
			padding: 20px 4px;
			box-sizing: border-box;

			.review_middle {
				.review_middle_box {
					.auto_overflow_wrap {
						.auto_overflow_contents {
							max-height: 86px;

							.auto_overflow_inner {
								.info_text {
									margin-bottom: 13px;
									@include mixins.multi_ellipsis(22px, 4, false);
									color: variables.$textColorBlack;
								}
							}
						}

						&.active {
							.auto_overflow_contents {
								.auto_overflow_inner {
									.info_text {
										@include mixins.clear_multi_ellipsis;
									}
								}
							}
						}

						.auto_overflow_footer {
							.overflow_footer_inner {
								@include mixins.justify_width(center, between);
							}

							margin-bottom: 13px;
						}
					}
				}
			}

			.review_top_common {
				.review_summary_wrap {
					margin-bottom: 14px;
				}
			}

			.review_button_common {
				@include mixins.justify_width(center, between);

				.btn_more_view {
					flex: 1 0;
				}

				.bottom_right {
					margin-left: auto;

					.btn_like {
						.ico_like {
							width: 20px;
							height: 20px;
							background: {
								position: center;
								repeat: no-repeat;
							}
							@include mixins.ratio_images('../images/ico_like_mok', 'png', 20px, 20px);
							margin-right: 4px;
						}

						&.active {
							.ico_like {
								@include mixins.ratio_images('../images/ico_like_pre_mok', 'png', 20px, 20px);
							}
						}

						.like_cont {
							position: relative;
							@include mixins.font_size_xs;
							display: inline-block;
							vertical-align: middle;
						}
					}

					.btn_reply {
						margin-left: 12px;

						&.active {
							.ico_reply {
								@include mixins.ratio_images('../images/ico_reply_pre_mok', 'png', 20px, 20px);
							}

							.text_reply {
								color: variables.$textColorBlack;
							}
						}

						.ico_reply {
							width: 20px;
							height: 20px;
							background: {
								position: center;
								repeat: no-repeat;
							}
							@include mixins.ratio_images('../images/ico_reply_mok', 'png', 20px, 20px);
							margin-right: 4px;
						}

						.text_reply {
							position: relative;
							@include mixins.font_size_xs;
							color: variables.$textColorLightGray;
							display: inline-block;
							vertical-align: middle;
						}
					}
				}
			}

			//썸네일 펼치기 형
			&.thumbnail {
				.review_middle {
					.review_middle_box {
						.auto_overflow_wrap {
							.auto_overflow_contents {
								max-height: 86px;

								.auto_overflow_inner {
									@include mixins.justify_width(top, between);

									.info_text {
										padding-right: 20px;
										@include mixins.multi_ellipsis(22px, 4, false);
									}

									.review_thumb {
										position: relative;
										width: 78px;
										height: 78px;

										.thumb_cont {
											display: block;
											width: 16px;
											height: 16px;
											line-height: 16px;
											position: absolute;
											bottom: 0;
											right: 0;
											background-color: rgba(0, 0, 0, .85);
											color: #fff;
											text-align: center;
											font-size: 11px;
											border-radius: 6px 0 6px 0;
										}
									}
								}

								.review_swiper {
									margin-top: 16px;
									display: none;

									.swiper-container {
										.swiper-wrapper {
											.swiper-slide {
												max-width: 335px;

												img {
													width: 100%;
												}
											}
										}
									}
								}
							}

							&.active {
								.auto_overflow_contents {
									.auto_overflow_inner {
										.info_text {
											width: 100%;
											padding-right: 0;
											@include mixins.clear_multi_ellipsis;
										}

										.review_thumb {
											display: none;
										}
									}

									.review_swiper {
										display: block;
									}
								}
							}
						}
					}
				}
			}

			//스포일러 형
			&.spoilers {
				.review_middle {
					.spoilers_text_box {
						height: 62px;
						background-color: variables.$colorGray06;
						color: variables.$textColorLightGray;
						@include mixins.justify_width(center, center);
						@include mixins.font_size_xs;
						border-radius: 6px;
						margin-bottom: 12px;

						.text_spoilers {
							&:before {
								content: '';
								display: inline-block;
								width: 17px;
								height: 19px;
								@include mixins.ratio_images('../images/ico_locker_mok', 'png', 17px, 19px);
								vertical-align: sub;
								margin-right: 6px;
							}
						}

						.btn_xxs {
							width: 20px;
							height: 20px;
							border: 0;
							background-color: rgba(0, 0, 0, 0);
							@include mixins.ratio_images('../images/ico_spo_more_mok', 'png', 20px, 20px);
							margin-left: 4px;
						}
					}
				}
			}

			//리뷰 답글 형
			&.reply {
				.review_reply {
					border-top: 1px solid variables.$colorGray04;
					padding-top: 10px;
					margin-top: 20px;

					.byte_check_wrap {

						&.active {
							display: block;
						}

						.form_textarea {
							height: 80px;
						}
					}

					.reply_list {
						.reply_list_item {
							position: relative;
							padding: 20px 20px 24px 37px;
							box-sizing: border-box;
							background-color: variables.$colorGray06;
							margin-top: 8px;
							border-radius: 10px;

							.review_user_info {

								&:before {
									content: '';
									display: inline-block;
									width: 8px;
									height: 9px;
									@include mixins.ratio_images('../images/arw_reply_mok', 'png', 8px, 9px);
									position: absolute;
									top: 24px;
									left: 20px;
								}
							}

							.info_text {
								color: variables.$textColorBlack;
							}
						}
					}

					.btn_more_body {
						margin-top: 12px;
					}
				}
			}

			//신고 형
			&.report_wrap {
				.review_middle {
					.report_text_box {
						background-color: variables.$colorGray06;
						border-radius: 6px;
						padding: 37px 0 16px;
						text-align: center;
						box-sizing: border-box;
						margin-top: 16px;
						margin-bottom: 12px;
						position: relative;

						.info_text {
							color: variables.$textColorLightGray;

							&:before {
								content: '';
								display: inline-block;
								width: 17px;
								height: 17px;
								@include mixins.ratio_images('../images/ico_exclamation_gray_sm_mok', 'png', 17px, 17px);
								vertical-align: middle;
								margin-right: 2px;
								position: absolute;
								top: 16px;
								left: 50%;
								transform: translateX(-50%);
							}
						}
					}
				}
			}
		}
	}

	.review_more_btn_wrap {
		margin-top: 24px;
		text-align: center;
	}
}
/* 리뷰 - 리뷰 없는 경우 */
.no_review_wrap {
	.no_review_inner {
		text-align: center;
		position: relative;
		height: 180px;
		padding-top: 100px;
		border-bottom: 1px solid variables.$colorGray04;
		box-sizing: border-box;

		.info_text {
			color: variables.$textColorLightGray;

			&:before {
				content: '';
				display: inline-block;
				position: absolute;
				top: 60px;
				left: 50%;
				width: 30px;
				height: 30px;
				margin-left: -15px;
				@include mixins.ratio_images('../images/ico_exclamation_gray_mok', 'png', 30px, 30px);
			}
		}
	}
}