@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

/* 팝업 - dialog */
body.dialog_open {
	position: fixed;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	overflow: hidden;
}
.dialog_wrapper {
	display: none;
	z-index: 1000;

	.ui-widget-overlay {
		background: #000;
		opacity: 0.5;
		filter: alpha(opacity=50);
		-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
	}

	.ui-dialog {
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		display: -webkit-flex;
		-moz-box-orient: vertical;
		-ms-flex-direction: column;
		-webkit-flex-direction: column;
		flex-direction: column;
		position: relative;
		width: 375px !important;
		max-height: calc(100% - 40px);
		margin: 0 20px;
		padding: 0;
		border: none;

		.ui-dialog-titlebar {
			display: none;
		}

		.btn_dialog_close {
			position: absolute;
			top: 17px;
			right: 17px;
			width: 30px;
			height: 30px;
			padding: 3px;

			.ico_dialog_close {
				position: relative;
				z-index: 3;
				display: block;
				width: 24px;
				height: 24px;
				@include mixins.ratio_images('../images/btn_dialog_close_mok', 'png', 24px, 24px);
			}
		}

		.dialog_wrap {
			&.ui-dialog-content {
				display: -webkit-box;
				display: -ms-flexbox;
				display: flex;
				display: -webkit-flex;
				-moz-box-orient: vertical;
				-ms-flex-direction: column;
				-webkit-flex-direction: column;
				flex-direction: column;
				position: relative;
				z-index: 1;
				overflow: hidden;
				padding: 0;
				background-color: #fff;
				border: none;
				border-radius: 16px;
			}

			&:after {
				content: '';
				display: block;
				min-height: inherit;
				font-size: 0;
			}

			.dialog_header {
				flex-shrink: 0;
				overflow: hidden;
				margin: 0 20px;
				padding: 18px 34px 19px 0;
				color: #000;
				text-align: left;
				background: #fff;
				border-bottom: 1px solid variables.$colorGray04;

				.dialog_title {
					@include mixins.font_size_lg;
					font-weight: variables.$fontWeightMedium;
					@include mixins.ellipsis(false, false, true);
				}
			}

			.dialog_contents {
				flex: 1;
				position: relative;
				overflow-y: auto;
				overflow-x: hidden;
				background: #fff;
				margin: 30px 20px 34px;
				-webkit-overflow-scrolling: touch;

				&:after {
					content: '';
					display: block;
					min-height: inherit;
					font-size: 0;
				}

				// 아이콘 영역
				.ico_info_wrap {
					text-align: center;

					.ico_img_box {
						margin-bottom: 20px;

						[class^=ico_] {
							display: inline-block;
							vertical-align: top;
						}
					}

					.ico_text_box {
						.ico_title {
							display: block;
							color: variables.$textColorBlack;
							@include mixins.font_size_def;
							font-weight: variables.$fontWeightBold;

							& ~ .ico_desc {
								margin-top: 12px;
							}
						}

						.ico_desc {
							display: block;
							color: variables.$textColorGray;
							@include mixins.font_size_xs;
							font-weight: variables.$fontWeightRegular;
						}
					}
				}

				.thumbnail_round_box {
					padding: 16px;
					border: 1px solid variables.$colorGray04;
					border-radius: 10px;

					.thumbnail_product_item {
						.thumbnail_area {
							width: 46px;
						}

						.info_area {
							padding: 6px 0 6px 12px;

							.info_product_name {
								.link_text {
									@include mixins.font_size_sm;
								}
							}
						}
					}

					+ .info_text_box {
						margin-top: 10px;
					}
				}
			}

			.dialog_footer {
				@include mixins.justify_width(center, center);
				margin-top: 34px;

				[class^=btn_] {
					flex: 1;
					min-width: auto;
					border-radius: 0;
				}
			}

			/* has btn */
			&.has_btn {
				.dialog_contents {
					margin-bottom: 0;
				}
			}

			/* no title */
			&.no_title {
				.dialog_contents {
					margin-top: 48px;
				}
			}

			/* no title line */
			&.no_title_line {
				.dialog_header {
					border-bottom: none;
				}

				.dialog_contents {
					margin-top: 0;
				}
			}
		}

		/* full popup */
		&.dialog_full {
			min-width: 100%;
			max-height: 100%;
			height: 100% !important;
			margin: 0;

			.dialog_wrap {
				width: 100% !important;
				height: 100% !important;
				max-height: 100% !important;
				display: -webkit-box;
				display: -webkit-flex;
				display: -ms-flexbox;
				display: flex;
				flex-direction: column;
				border-radius: 0;

				.dialog_header {
					margin: 0;
					padding: 18px 54px 17px;

					.dialog_title {
						text-align: center;
					}
				}

				.dialog_contents {
					margin-top: 24px;
				}

				.dialog_footer {
					margin: 34px 0 30px;
					padding: 0 20px;

					[class^=btn_] {
						border-radius: 6px;

						& ~ [class^=btn_] {
							margin-left: 5px;
						}
					}
				}

				&.has_btn {
					.dialog_contents {
						margin-bottom: 0;
					}
				}

				&.no_title_line {
					.dialog_header {
						border-bottom: none;
					}

					.dialog_contents {
						margin-top: 0;
					}
				}
			}
		}

		/* fixed bottom Popup */
		&.dialog_bottom {
			align-self: flex-end;
			width: 100% !important;
			max-height: calc(100% - 134px) !important;

			margin: 0;
			@include mixins.transform(translateY(100%));
			-webkit-backface-visibility: hidden;

			.dialog_wrap {
				border-radius: 16px 16px 0 0;

				.dialog_header {
					margin: 0;
					padding: 18px 54px 19px;

					.dialog_title {
						text-align: center;
					}
				}

				.dialog_contents {
					margin: 24px 20px 30px;
				}

				.dialog_footer {
					margin: 34px 0 30px;
					padding: 0 20px;

					[class^=btn_] {
						border-radius: 6px;

						& ~ [class^=btn_] {
							margin-left: 5px;
						}
					}
				}

				&.has_btn {
					.dialog_contents {
						margin-bottom: 0;
					}
				}

				&.arw_close {
					.btn_dialog_close {
						padding: 0;
						top: 4px;
						right: calc(50% - 13px);

						.ico_dialog_close {
							width: 25px;
							height: 25px;
							background: {
								position: center;
								repeat: no-repeat;
							}
							@include mixins.ratio_images('../images/ico_dialog_close_arw_mok', 'png', 25px, 25px);
						}
					}

					.dialog_contents {
						margin-top: 32px;
					}
				}

				&.outer_close {
					overflow: visible;
					padding-top: 17px;

					.btn_dialog_close {
						width: 36px;
						height: 36px;
						padding: 0;
						top: -50px;
						right: calc(50% - 18px);

						.ico_dialog_close {
							width: 36px;
							height: 36px;
							background: {
								position: center;
								repeat: no-repeat;
							}
							@include mixins.ratio_images('../images/ico_dialog_close_outer_mok', 'png', 36px, 36px);
						}
					}
				}
			}
		}

		/* alert popup */
		&.dialog_alert {
			max-width: 335px !important;

			.dialog_wrap {
				.dialog_contents {
					margin: 0;
					padding: 30px 30px;
					text-align: center;

					.alert_text {
						@include mixins.justify_width(center, center);
						width: 100%;
						min-height: 68px;
						color: #000;
						@include mixins.font_size_md;
						font-weight: variables.$fontWeightMedium;
					}
				}

				.dialog_footer {
					margin-top: 0;
				}
			}
		}

		/* tooltip popup */
		&.dialog_tooltip {
			width: 295px !important;

			.dialog_wrap {
				.btn_dialog_close {
					padding: 5px;

					.ico_dialog_close {
						width: 20px;
						height: 20px;
						background: {
							position: center;
							repeat: no-repeat;
						}

						@include mixins.ratio_images('../images/btn_dialog_close_sm_mok', 'png', 20px, 20px);
					}
				}

				.dialog_header {
					border-bottom: none;
					padding: 19px 26px 19px;

					.dialog_title {
						@include mixins.font_size_md;
						text-align: center;
					}
				}

				.dialog_contents {
					margin: 0 20px 24px;

					.tooltip_sub_title {
						color: variables.$textColorBlack;
						@include mixins.font_size_sm;
						font-weight: variables.$fontWeightBold;

						& ~ .tooltip_desc {
							margin-top: 6px;
						}
					}

					.tooltip_desc {
						color: variables.$textColorBlack;
						@include mixins.font_size_xs;
						font-weight: variables.$fontWeightRegular;

						& ~ .tooltip_sub_title {
							margin-top: 16px;
						}

						& ~ .tooltip_desc {
							margin-top: 6px;
						}

						.btn_text_link {
							display: inline;
							color: inherit;
							font-size: inherit;
							line-height: inherit;

							.text {
								display: inline;
								text-decoration: underline;
							}
						}
					}

					.tooltip_info_list {
						display: block;

						.tooltip_info_item {
							display: block;
							color: variables.$textColorBlack;
							box-sizing: border-box;

							& ~ .tooltip_info_item {
								margin-top: 11px;
								padding-top: 13px;
								border-top: 1px solid variables.$colorGray04;
							}
						}
					}
				}
			}
		}

		@include mixins.respond-to(mobileSm) {
			width: calc(100% - 40px) !important;
		}
	}

	&.open {
		@include mixins.justify_width(center, center);
		overflow: hidden;
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;

		.ui-dialog {
			&.dialog_bottom {
				animation: bottomPopAnimation 0.23s cubic-bezier(0.24, 0.6, 0.65, 1.01) forwards;
				-webkit-animation: bottomPopAnimation 0.23s cubic-bezier(0.24, 0.6, 0.65, 1.01) forwards;
			}
		}
	}
}
@keyframes bottomPopAnimation {
	0% {
		@include mixins.transform(translateY(100%));
	}

	100% {
		@include mixins.transform(translateY(0));
	}
}

/* sps */
.wrapper {
	.header_wrapper {
		&.scroll_down {
			~ .container_wrapper {
				.sps {
					&.sps-blw:not(.sps_add) {
						top: 0;
					}
				}
			}
		}

		&.scroll_up {
			~ .container_wrapper {
				.sps {
					&.sps-blw {
						top: 60px;
					}
				}
			}
		}
	}
}
.sps {
	&.sps_scroll_fixed {
		position: absolute;
		width: 100%;
		background-color: #fff;
		z-index: 10;
		@include mixins.transition(top .23s);

		~ .sps-bg {
			position: absolute;
			top: 0;
			left: 0;
		}

		&.sps-blw {
			position: fixed;
			left: 0;
			right: 0;
			width: 100%;
		}
	}
}

/* rating */
.rating-container {
	position: relative;
	display: inline-block;
	text-align: left;
	font-size: 0;
	vertical-align: top;
	margin-left: -2px;

	.clear-rating {
		display: none;
	}

	.rating-stars {

		.empty-stars {
			.star {
				margin: 0 2px;

				.glyphicon-star-empty:before {
					content: '';
					display: inline-block;
					width: 22px;
					height: 22px;
					@include mixins.ratio_images('../images/ico_klover_none_mok', 'png', 22px, 22px);
				}
			}
		}

		.filled-stars {
			.star {
				margin: 0 2px;

				.glyphicon-star:before {
					content: '';
					display: inline-block;
					width: 22px;
					height: 22px;
					@include mixins.ratio_images('../images/ico_klover_mok', 'png', 22px, 22px);
				}
			}

		}

		.form_rating {
			color: variables.$textColorBlack;
			@include mixins.font_size_xs;
			font-family: variables.$fontEng;
			outline: 0;
		}
	}

	.caption {
		display: none;
	}

	&.rating-sm {
		margin-left: -2px;

		.empty-stars {
			.star {
				margin: 0 2px;

				.glyphicon-star-empty:before {
					content: '';
					display: inline-block;
					width: 18px;
					height: 18px;
					background: {
						repeat: no-repeat;
						position: center;
					}
					@include mixins.ratio_images('../images/ico_klover_sm_none_mok', 'png', 18px, 18px);
				}
			}
		}

		.filled-stars {
			.star {
				margin: 0 2px;

				.glyphicon-star:before {
					content: '';
					display: inline-block;
					width: 18px;
					height: 18px;
					background: {
						repeat: no-repeat;
						position: center;
					}
					@include mixins.ratio_images('../images/ico_klover_sm_mok', 'png', 18px, 18px);
				}
			}
		}
	}

	&.rating-xs {
		margin-left: -1px;

		.empty-stars {
			.star {
				margin: 0 2px 0 1px;

				.glyphicon-star-empty:before {
					content: '';
					display: inline-block;
					width: 13px;
					height: 13px;
					background: {
						repeat: no-repeat;
						position: center;
					}
					@include mixins.ratio_images('../images/ico_klover_xs_none_mok', 'png', 13px, 13px);
				}
			}
		}

		.filled-stars {
			.star {
				margin: 0 2px 0 1px;

				.glyphicon-star:before {
					content: '';
					display: inline-block;
					width: 13px;
					height: 13px;
					background: {
						repeat: no-repeat;
						position: center;
					}
					@include mixins.ratio_images('../images/ico_klover_xs_mok', 'png', 13px, 13px);
				}
			}
		}
	}

	&.rating-lg {
		margin-left: -6px;

		.empty-stars {
			.star {
				margin: 0 6px;

				.glyphicon-star-empty:before {
					content: '';
					display: inline-block;
					width: 40px;
					height: 40px;
					background: {
						repeat: no-repeat;
						position: center;
					}
					@include mixins.ratio_images('../images/ico_klover_lg_none_mok', 'png', 40px, 40px);
				}
			}
		}

		.filled-stars {
			.star {
				margin: 0 6px;

				.glyphicon-star:before {
					content: '';
					display: inline-block;
					width: 40px;
					height: 40px;
					background: {
						repeat: no-repeat;
						position: center;
					}
					@include mixins.ratio_images('../images/ico_klover_lg_mok', 'png', 40px, 40px);
				}
			}
		}
	}

	&.has_caption {
		.caption {
			display: inline-block;
			margin: 0 0 0 8px;
			vertical-align: top;
			font-size: 0;

			.label {
				padding: 0;
				border-radius: 0;
				font-size: 0;

				.val {
					@include mixins.font_size_xl;
					color: variables.$textColorGreen;
					font-weight: variables.$fontWeightBold;
					line-height: 1;
				}

				.total {
					padding-top: 5px;
					@include mixins.font_size_sm;
					color: variables.$textColorLightGray;
					line-height: 1;

					&:before {
						content: '/';
					}
				}
			}
		}
	}
}

.rating-disabled {
	.rating-input {
		cursor: default;
	}

	.rating-stars {
		cursor: default;
	}
}
.total_rating_box {
	.caption {
		margin: 1px 0 0 8px;
		vertical-align: top;

		.label {
			padding: 0;
			border-radius: 0;
			font-size: 0;

			.val {
				@include mixins.font_size_xl;
				color: variables.$textColorGreen;
				font-weight: variables.$fontWeightBold;
				line-height: 1;
			}

		}
	}

	.total_score {
		display: inline-block;
		margin: 7px 0 0 1px;
		@include mixins.font_size_sm;
		color: variables.$textColorLightGray;
		line-height: 1;
		vertical-align: top;
	}

}

/* spinner */
.form_spinner_box {
	display: inline-block;
	vertical-align: top;

	.ui-spinner {
		position: relative;
		display: block;
		width: 102px;
		padding: 0 33px;
		background-color: #fff;
		box-sizing: border-box;
		font-size: 0;

		.form_spinner {
			width: 100%;
			height: 36px;
			margin: 0;
			padding: 1px 0 0 0;
			border-top: 1px solid variables.$colorGray02;
			border-bottom: 1px solid variables.$colorGray02;
			color: variables.$textColorBlack;
			@include mixins.font_size_sm;
			font-family: variables.$fontEng;
			outline: 0;
			text-align: center;
			box-sizing: border-box;
			vertical-align: top;
		}

		.ui-spinner-button {
			display: block;
			top: 0;
			width: 34px;
			height: 36px;
			background-color: #fff;
			border: 1px solid variables.$colorGray02;
			box-sizing: border-box;

			.ui-icon {
				width: 12px;
				height: 12px;
				@include mixins.ratio_images('../images/ico_spinner_down_mok', 'png', 12px, 12px);
				text-align: center;
				vertical-align: top;
			}

			&.ui-spinner-button.ui-spinner-up {
				.ui-icon {
					@include mixins.ratio_images('../images/ico_spinner_up_mok', 'png', 12px, 12px);
				}
			}

		}

		.ui-spinner-button.ui-spinner-up {
			border-left: none;
			border-radius: 0 6px 6px 0;
		}

		.ui-spinner-button.ui-spinner-down {
			left: 0;
			border-right: none;
			border-radius: 6px 0 0 6px;
		}

		&.form_spinner_box.disabled {
			.ui-spinner {
				.ui-spinner-button {
					.ui-icon {
						@include mixins.ratio_images('../images/ico_spinner_down_off_mok', 'png', 12px, 12px);
					}

					&.ui-spinner-button.ui-spinner-up {
						.ui-icon {
							@include mixins.ratio_images('../images/ico_spinner_up_off_mok', 'png', 12px, 12px);
						}
					}
				}
			}
		}
	}
}

/* datepicker */
.datepicker_box {
	position: relative;
	width: 100%;

	.form_datepicker {
		display: inline-block;
		width: 100%;
		height: 50px;
		padding: 0 14px;
		color: variables.$textColorBlack;
		font-size: variables.$fontSizeSm;
		line-height: 48px;
		letter-spacing: variables.$letterSpacing;
		font-family: variables.$fontKor;
		background-color: #fff;
		border: 1px solid variables.$colorGray03;
		border-radius: 6px;
		box-sizing: border-box;
		@include mixins.transition(border-color .2s ease-out);
		outline: none;

		&:active, &:focus {
			border-color: #000;
		}

		&[disabled], &[aria-disabled], &[readonly] {
			color: variables.$textColorLightGray;
			background-color: variables.$colorGray06;
			pointer-events: none;
			cursor: default;
		}

		@include mixins.set_placeholder_color(variables.$textColorLightGray);
	}

	.ui-datepicker-trigger {
		position: absolute;
		right: 0;
		top: 50%;
		transform: translateY(-50%);
		width: 36px;
		height: 36px;
		background: #ff1493; // 임시
	}
}

/* swiper */
// 4버전 & 7버전 호환 필요
.swiper-container {
	margin-left: auto;
	margin-right: auto;
	position: relative;
	overflow: hidden;
	list-style: none;
	padding: 0;
	// Fix of Webkit flickering
	z-index: 1;

	// a11y
	.swiper-notification {
		position: absolute;
		left: 0;
		top: 0;
		pointer-events: none;
		opacity: 0;
		z-index: -1000;
	}
}

.swiper-pagination {
	&.swiper-pagination-fraction {
		left: 50%;
		bottom: 14px;
		transform: translateX(-50%);
		width: auto;
		min-width: 60px;
		height: 29px;
		padding: 0 14px;
		line-height: 29px;
		border-radius: 20px;
		background-color: rgba(0, 0, 0, .6);
		box-sizing: border-box;
		font-size: 0;

		.swiper-pagination-current,
		.swiper-pagination-total {
			display: inline-block;
			color: variables.$colorGray02;
			font-size: variables.$fontSizeXs;
			letter-spacing: variables.$letterSpacing;
			line-height: 29px;
			font-family: variables.$fontEng;
			vertical-align: top;
		}

		.swiper-pagination-current {
			color: #fff;
			font-weight: variables.$fontWeightBold;
		}

		.swiper-pagination-total {
			&:before {
				content: '-';
				display: inline-block;
				margin: 0 4px 0 3px;
				color: variables.$colorGray02;
			}
		}
	}
}

.swiper_control_box {
	text-align: center;
	font-size: 0;

	.swiper-pagination {
		display: inline-block;
		position: static;
		min-width: auto;
		margin-top: 24px;
		-webkit-transform: translateX(0);
		transform: translateX(0);
		vertical-align: top;

		.swiper-pagination-bullet {
			width: 6px;
			height: 6px;
			margin: 0 3px 0 2px;
			background: variables.$colorGray03;
			@include mixins.transition(background 0.4s);

			&.swiper-pagination-bullet-active {
				background: #000;
			}
		}

		&.swiper-pagination-fraction {
			bottom: 0;
			height: 16px;
			padding: 0 2px;
			margin-top: 0;
			border-radius: 0;
			background: none;

			.swiper-pagination-current,
			.swiper-pagination-total {
				color: variables.$textColorLightGray;
				@include mixins.font_size_xs;
			}

			.swiper-pagination-current {
				color: variables.$textColorBlack;
				font-weight: variables.$fontWeightMedium;
			}

			.swiper-pagination-total {
				&:before {
					margin: 0 3px;
					color: variables.$textColorLightGray;
				}
			}
		}
	}
}

.slider_item {
	height: 4px;
	background: #eaeaea;
	margin: 7px 0;
	border-radius: 2px;

	.ui-slider-range {
		background: #767675;
		border-radius: 2px;
	}

	.ui-slider-handle {
		width: 16px;
		height: 16px;
		margin-left: -9px;
		background: #fff;
		border: 1px solid #999;
		border-radius: 9px;
		top: -6px;
	}

	&.text_size_slider {
		position: relative;
		height: 32px;
		margin: 0 17px;
		background: #f7f7f7;
		border: 1px solid #eaeaea;

		&:before, &:after {
			content: '';
			position: absolute;
			display: inline-block;
			width: 16px;
			height: 32px;
			border-radius: 17px;
			border: 1px solid transparent;
			top: -1px;
		}
		&:before {
			left: -18px;
			background: #5055b1;
			border-color: #5055b1;
			border-top-right-radius: 0;
			border-bottom-right-radius: 0;
		}
		&:after {
			right: -16px;
			background: #f7f7f7;
			border-color: #eaeaea;
			border-top-left-radius: 0;
			border-bottom-left-radius: 0;
			border-left: none;
		}
		.ui-slider-range {
			background: #5055b1;
			border-radius: 17px;
			border-top-right-radius: 0;
			border-bottom-right-radius: 0;
			border-top-left-radius: 0;
			border-bottom-left-radius: 0;
			height: 34px;
			top: -1px;
		}

		.ui-slider-handle {
			width: 32px;
			height: 32px;
			margin-left: -18px;
			top: -1px;
			border-color: #ccc;
			border-radius: 17px;
			z-index: 3;
		}
	}
}

.slider_wrap {
	position: relative;

	&.has_pagination {
		@include mixins.justify_width(center);
		.preview_pagination {
			color: #767676;
			font-size: 0;

			.pagination_current, .pagination_total {
				display: inline-block;
				font-size: 13px;
				line-height: 18px;
			}

			.pagination_current {
				color: #000;
				font-weight: variables.$fontWeightMedium;
			}
			.pagination_total {
				&:before {
					content: "-";
					display: inline-block;
					margin: 0 4px 0 3px;
				}
			}

		}
		.slider_item {
			flex: 1;
			margin-left: 20px;
		}
	}

	.slider_text {
		position: absolute;
		display: inline-block;
		font-size: variables.$fontSizeXxs;
		top: 50%;
		z-index: 2;
		transform: translateY(-50%);

		.prefix {
			font-size: variables.$fontSizeSm;
		}
		&.minus {
			color: #fff;
			left: 16px;
		}
		&.plus {
			right: 16px;
		}
	}
}