@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

.ico_speech{
	display: inline-block;
	width: 16px;
	height: 16px;
	background: {
		position: center;
		repeat: no-repeat;
	}
	@include mixins.ratio_images('../images/ico_title_message_mok', 'png', 16px, 16px);
	vertical-align: top;
}

.ico_step_store_guide,
.ico_step_store_visit,
.ico_step_delivery {
	display: inline-block;
	width: 58px;
	height: 58px;
	background: {
		repeat: no-repeat;
	}
	vertical-align: top;
}

.ico_step_store_guide {
	@include mixins.ratio_images('../images/ico_step_store_guide_mok', 'png', 58px, 58px);
}

.ico_step_store_visit {
	@include mixins.ratio_images('../images/ico_step_store_visit_mok', 'png', 58px, 58px);
}

.ico_step_delivery {
	@include mixins.ratio_images('../images/ico_step_delivery_mok', 'png', 58px, 58px);
}

