@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

/* tab */
.tab_wrap {
	position: relative;
	width: 100%;
	padding: 0;
	box-sizing: border-box;

	&:not(&.type_fill, &.justify){
		.tab_list_wrap {
			.tabs {
				padding: 0 20px;
				@include mixins.scrollX;

				.tab_item{
					white-space: nowrap;
				}
			}
		}
	}

	// tab - tab_list_wrap
	.tab_list_wrap {
		@include mixins.justify_width(center);
		width: 100%;
		height: 48px;
		margin-bottom: 30px; // TODO : 여백 가이드 나오면 수정
		background-color: #fff;
		border-bottom: 1px solid variables.$colorGray03;
		box-sizing: border-box;

		.tabs {
			position: relative;
			@include mixins.justify_width;
			height: calc(100% + 1px);
			flex: 1;
			padding: 0;
			box-sizing: border-box;


			.tab_item {
				position: relative;
				flex: 0 auto;
				height: 100%;
				margin: 0;
				padding: 0 14px;
				background: none;
				box-sizing: border-box;
				white-space: normal;

				&:first-child {
					padding-left: 0;
				}
				&:last-child {
					padding-right: 0;
				}

				.tab_link{
					@include mixins.justify_width(center, center);
					align-items: stretch;
					position: relative;
					width: 100%;
					height: 100%;
					padding: 0;
					text-align: center;
					box-sizing: border-box;

					&:after {
						content: '';
						position: absolute;
						left: 0;
						bottom: 0;
						z-index: 2;
						width: 100%;
						height: 2px;
						background-color: transparent;
						@include mixins.transition(background-color .2s ease-out);
					}

					.tab_text{
						position: relative;
						display: block;
						width: 100%;
						color: variables.$textColorLightGray;
						@include mixins.font_size_def;
						font-weight: variables.$fontWeightRegular;
						font-family: variables.$fontKor;
						box-sizing: border-box;
						@include mixins.transition(color .2s ease-out, font-weight .2s ease-out);

						.num {
							margin-left: 1px;
							font-size: 11px;
						}
					}
				}

				&.ui-state-disabled {
					.tab_link {
						cursor: default;

						.tab_text {
							color: variables.$colorGray02; // TODO 추후 디자인 가이드 나오면 수정 필요
						}
					}
				}

				&.ui-state-active, &.active {
					z-index: 2;
					margin-bottom: 0;
					padding-bottom: 0;

					.tab_link {
						cursor: pointer;

						&:after {
							background-color: #000;
						}

						.tab_text {
							color: variables.$textColorBlack;
							font-weight: variables.$fontWeightBold;
						}
					}
				}
			}
		}

		.right_area {
			display: inline-block;
			flex: 0 auto;
			margin-left: 30px;
		}
	}

	// tab - content
	.tab_content {
		padding: 0;
		background: #fff;
		box-sizing: border-box;
	}
}

/* tab - sm 타입 */
.tab_wrap.type_sm {
	.tab_list_wrap {
		@include mixins.justify_width(start);
		height: 37px;
		padding: 0;

		.tabs {
			.tab_item {
				padding: 0 12px;

				&:first-child {
					padding-left: 0;
				}
				&:last-child {
					padding-right: 0;
				}

				.tab_link {
					&:after {
						height: 3px;
					}

					.tab_text {
						@include mixins.font_size_sm;
					}
				}
			}
		}
	}
}

/* tab - fill 타입 */
.tab_wrap.type_fill {
	.tab_list_wrap {
		height: 48px;
		border-bottom: 0;

		.tabs {
			height: 100%;

			.tab_item {
				flex: 1;
				border: 1px solid variables.$colorGray03;
				padding: 0;
				@include mixins.transition(background .2s ease-out, border-color .2s ease-out);

				.tab_link {
					padding: 0 14px; // TODO :: 추후 디자인 나오면 좌우 여백 값 수정 필요

					&:after {
						display: none;
					}

					.tab_text {
						padding-bottom: 1px;
					}
				}

				&.ui-tabs-active {
					background-color: variables.$kyoboPurple;
					border-color: variables.$kyoboPurple;

					.tab_text {
						color: #fff;
					}
				}

				& ~ .tab_item {
					margin-left: -1px;
				}
			}
		}
	}
}

/* tab - line 타입 */
.tab_wrap.type_line_primary {
	.tab_list_wrap {
		position: relative;
		height: 44px;
		border-bottom: none;
		margin-bottom: 24px;

		&:after{
			content: '';
			display: block;
			position: absolute;
			left: 0;
			bottom: 0;
			width: 100%;
			height: 2px;
			background-color: variables.$kyoboPurple;
		}

		.tabs {
			height: 100%;
			box-sizing: border-box;

			.tab_item {
				padding: 0;
				background: #fff;
				border: 1px solid variables.$colorGray03;
				border-bottom: 0;
				border-radius: 6px 6px 0 0;
				@include mixins.transition(border-color .23s);

				.tab_link {
					padding: 0 14px;

					.tab_text {
						padding-bottom: 2px;
						@include mixins.font_size_sm;
					}

					&:after {
						bottom: 0;
						@include mixins.transition(none);
					}
				}

				&.ui-state-active {
					border: 2px solid variables.$kyoboPurple;
					border-bottom: 0;

					.tab_link {
						.tab_text {
							color: variables.$textColorPurple;
						}

						&:after {
							background: #fff;
						}
					}
				}

				& ~ .tab_item {
					margin-left: -1px;
				}
			}
		}
	}
}

/* tab - 태그 타입 */
.tab_wrap.type_tag{
	.tab_list_wrap{
		display: block;
		height: auto;
		border-bottom: none;
		padding: 0;

		.tabs{
			overflow: visible;
			display: block;
			height: auto;
			margin: -3px -3px;

			.tab_item{
				padding: 3px 3px;

				.tab_link{
					background-color: variables.$colorGray06;
					border: 1px solid variables.$colorGray06;
					border-radius: 20px;
					box-sizing: border-box;
					@include mixins.transition(background-color .23s, border-color .23s);

					.tab_text{
						@include mixins.font_size_sm;
						padding: 5px 14px 6px;
						@include mixins.transition(color .23s);
					}

					&:after{
						content: none;
					}
				}

				&.ui-state-active{
					.tab_link{
						background-color: variables.$textColorGray;
						border-color: variables.$textColorGray;

						.tab_text{
							font-weight: variables.$fontWeightRegular;
							color: #fff;
						}
					}
				}
			}
		}
	}
}

/* tab - fill round 타입 - 회원(로그인) 사용중 */
.tab_wrap.type_fill_round {
	> .tab_list_wrap {
		height: 44px;
		border-bottom: none;
		margin-bottom: 30px;

		> .tabs {
			.tab_item {
				border: 1px solid variables.$colorGray03;
				border-bottom-color: variables.$kyoboPurple;
				border-radius: 10px 10px 0 0;
				background-color: variables.$colorGray06;
				padding: 0;
				@include mixins.transition(background .3s ease-out, border-color .3s ease-out);

				.tab_link {
					padding: 0 14px; // TODO :: 추후 디자인 나오면 좌우 여백 값 수정 필요

					.tab_text {
						padding-bottom: 1px;
						@include mixins.font_size_sm;
					}

					&:after {
						display: none;
					}
				}

				&.ui-state-active {
					background-color: variables.$kyoboPurple;
					border-color: variables.$kyoboPurple;

					.tab_text {
						color: #fff;
						font-weight: variables.$fontWeightBold;
					}
				}

				& ~ .tab_item {
					margin-left: -1px;
				}
			}
		}
	}
}

/* tab - 균등형 */
.tab_wrap.justify {
	> .tab_list_wrap {
		.tabs {
			width: 100%;

			.tab_item {
				flex: 1;
				padding: 0;

				.tab_link {
					padding: 0 14px; // TODO :: 추후 디자인 나오면 좌우 여백 값 수정 필요
				}
			}
		}
	}
}

.contents_wrap{
	.contents_inner{
		.tab_wrap{
			&:not(&.type_sm, &.type_fill, &.type_fill){
				.tab_list_wrap {
					.tabs {
						padding: 0;
					}
				}
			}
		}
	}
}