@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

/*===== 상품 섬네일 아이템 =====*/

.thumbnail_product_item {
	position: relative;

	&:after {
		content: '전시 템플릿 정의 후 작업';
		position: absolute;
		display: inline-block;
		color: #fff;
		width: 100%;
		height: 100%;
		left: 0;
		top: 0;
		background-color: rgba(255, 0, 0, .5);
		pointer-events: none;
	}

	// 상품 섬네일 세로형
	&.vertical_type {
		width: 134px;

		.thumbnail_area {
			display: block;
			overflow: hidden;
			width: 100%;
			min-height: 10px;
			box-sizing: border-box;
			border: 1px solid variables.$colorGray04;
			background-color: variables.$colorGray04;

			.link_thumbnail {
				display: block;
				width: 100%;

				img {
					display: block;
					width: 100%;
				}
			}
		}

		.info_area {
			display: block;
			width: 100%;
			margin-top: 16px;
			box-sizing: border-box;

			&.align_center {
				margin-top: 10px;
				text-align: center;

				[class^='info_'] {
					text-align: center;
				}

				.info_product_name {
					@include mixins.font_size_xxs;
					@include mixins.multi_ellipsis(variables.$fontSizeXxsLineHeight, 2, false);

					.link_text {
						@include mixins.font_size_xxs;
						text-align: center;
					}
				}
			}

			.info_product_name {
				@include mixins.font_size_sm;
				//font-weight: variables.$fontWeightMedium;
				color: variables.$textColorBlack;
				@include mixins.multi_ellipsis(variables.$fontSizeSmLineHeight, 2, false);

				.link_text {
					display: block;
					width: 100%;
					@include mixins.font_size_sm;
					//font-weight: variables.$fontWeightMedium;
					color: variables.$textColorBlack;

					&.multi_ellipsis {
						@include mixins.multi_ellipsis(23px, 2, false);
					}
				}
			}

			.info_author_publication {
				margin-top: 5px;

				.info_author {
					display: inline-block;
					@include mixins.font_size_xxs;
					font-weight: variables.$fontWeightRegular;
					color: variables.$textColorGray;

					&.multi_ellipsis {
						@include mixins.multi_ellipsis(19px, 2, false);
					}
				}

				.info_publication {
					display: inline-block;
					@include mixins.font_size_xxs;
					font-weight: variables.$fontWeightRegular;
					color: variables.$textColorGray;

					&.multi_ellipsis {
						@include mixins.multi_ellipsis(19px, 2, false);
					}
				}

				[class^='info_'] + [class^='info_'] {
					position: relative;
					padding-left: 6px;

					&:before {
						content: '';
						position: absolute;
						left: 0;
						top: 8px;
						display: block;
						width: 2px;
						height: 2px;
						border-radius: 50%;
						background-color: variables.$textColorGray;
					}
				}
			}

			.info_price {
				margin-top: 12px;

				[class^='price_'] {
					display: inline-block;
					@include mixins.font_size_xs;
				}

				.price_dc {
					font-weight: variables.$fontWeightBold;
					color: variables.$textColorGreen;
					font-family: variables.$fontEng;
				}

				.price_amount {
					font-weight: variables.$fontWeightRegular;
					color: variables.$textColorBlack;

					strong {
						font-weight: variables.$fontWeightBold;
						color: variables.$textColorBlack;
						font-family: variables.$fontEng;
					}
				}

				.price_point {
					@include mixins.font_size_xxs;
					font-weight: variables.$fontWeightRegular;
					color: variables.$textColorGray;
					font-family: variables.$fontEng;
				}
			}
		}

		.product_check {
			margin-bottom: 6px;
		}
	}

	/* 상품 섬네일 가로형 */
	&.horizontal_type {
		@include mixins.justify_width(center, start, row);
		width: auto;
		min-width: 250px;
		min-height: 10px;

		.thumbnail_area {
			flex-shrink: 0;
			display: block;
			overflow: hidden;
			width: 84px;
			min-height: 10px;
			box-sizing: border-box;
			border: 1px solid variables.$colorGray04;
			background-color: variables.$colorGray04;
			flex-shrink: 0;

			.link_thumbnail {
				display: block;
				width: 100%;

				img {
					display: block;
					width: 100%;
				}
			}
		}

		.info_area {
			display: block;
			width: auto;
			box-sizing: border-box;
			padding-left: 12px;

			.info_product_number {
				@include mixins.font_size_xs;
				font-weight: variables.$fontWeightRegular;
				font-family: variables.$fontEng;
				color: variables.$textColorGray;
			}

			.info_product_name {
				@include mixins.font_size_xs;
				font-weight: variables.$fontWeightMedium;
				color: variables.$textColorBlack;
				@include mixins.multi_ellipsis(variables.$fontSizeXsLineHeight, 2, false);

				.link_text {
					display: block;
					width: 100%;
					@include mixins.font_size_xs;
					font-weight: variables.$fontWeightMedium;
					color: variables.$textColorBlack;

					&.multi_ellipsis {
						@include mixins.multi_ellipsis(23px, 2, false);
					}
				}
			}

			.info_author_publication {
				margin-top: 5px;

				.info_author {
					display: inline-block;
					@include mixins.font_size_xxs;
					font-weight: variables.$fontWeightRegular;
					color: variables.$textColorGray;

					&.multi_ellipsis {
						@include mixins.multi_ellipsis(19px, 2, false);
					}
				}

				.info_publication {
					display: inline-block;
					@include mixins.font_size_xxs;
					font-weight: variables.$fontWeightRegular;
					color: variables.$textColorGray;

					&.multi_ellipsis {
						@include mixins.multi_ellipsis(19px, 2, false);
					}
				}

				[class^='info_'] + [class^='info_'] {
					position: relative;
					padding-left: 6px;

					&:before {
						content: '';
						position: absolute;
						left: 0;
						top: 8px;
						display: block;
						width: 2px;
						height: 2px;
						border-radius: 50%;
						background-color: variables.$textColorGray;
					}
				}
			}

			.info_price {
				margin-top: 20px;

				[class^='price_'] {
					display: inline-block;
					@include mixins.font_size_xs;
				}

				.price_dc {
					font-weight: variables.$fontWeightBold;
					color: variables.$textColorGreen;
					font-family: variables.$fontEng;
				}

				.price_amount {
					font-weight: variables.$fontWeightRegular;
					color: variables.$textColorBlack;

					strong {
						font-weight: variables.$fontWeightBold;
						color: variables.$textColorBlack;
						font-family: variables.$fontEng;
					}
				}

				.price_point {
					@include mixins.font_size_xxs;
					font-weight: variables.$fontWeightRegular;
					color: variables.$textColorGray;
					font-family: variables.$fontEng;
				}
			}

			.rating-container {
				margin-top: 18px;
			}
		}
	}

	/* 섬네일 사이즈 별 */
	&.vertical_type {
		// 스타일 가이드 common guide 기준. Medium Size로 잡고 default value로 지정
		&.width_md {
			// width: 134px;
			.info_area {
				// margin-top: 16px;
				&.align_center {
					// margin-top: 10px;
				}

				.info_author_name {
					// margin-top: 5px;
				}

				.info_price {
					// margin-top: 12px;
				}
			}
		}

		&.width_sm {
			width: 122px;

			.info_area {
				margin-top: 16px;

				&.align_center {
					margin-top: 10px;
				}

				.info_author_name {
					margin-top: 5px;
				}

				.info_price {
					margin-top: 12px;
				}
			}
		}
	}

}

/* 섬네일 리스트 */
[class*='thumbnail_list'] {
	@include mixins.justify_width(start, start, row);
	box-sizing: border-box;

	&.row_stack {
		flex-wrap: wrap;
	}

	/* Vertical Type List */
	&.thumbnail_list_vertical {
		margin: -8px;

		.thumbnail_item {
			padding: 8px;
			box-sizing: border-box;
		}
	}

	/* Horizontal Type List */
	&.thumbnail_list_horizontal {
		margin: -8px;

		.thumbnail_item {
			padding: 8px;
			box-sizing: border-box;

			&:first-child {
				.thumbnail_product_item.horizontal_type {
					.info_area {
						.info_product_number {
							font-weight: variables.$fontWeightMedium;
							color: variables.$textColorGreen;
						}
					}
				}
			}
		}

		&.row_stack {
			min-width: auto;

			.thumbnail_item {
				.thumbnail_product_item {
					min-width: auto;
				}
			}
		}

		&.full_width {
			flex-wrap: wrap;

			.thumbnail_item {
				width: 100%;
				min-width: 100%;
			}
		}

		&.align_top {
			.thumbnail_item {
				.thumbnail_product_item {
					&.horizontal_type {
						@include mixins.justify_width(start, start, row);

						.info_area {
							padding-top: 10px;
						}
					}
				}
			}
		}
	}
}
.swiper-container {
	&.scrollbar_text {
		.swiper-wrapper.thumbnail_list_vertical {
			padding-bottom: 20px;

			.thumbnail_item.swiper-slide {
				width: auto;
			}
		}

		.swiper-wrapper {
			.swiper-slide {
				.thumbnail_list_vertical {
					.thumbnail_item {
						width: auto;
					}
				}
			}
		}

		&.view_type_img {
			.swiper-wrapper.thumbnail_list_vertical {

			}
		}

		&.view_type_txt {
			.swiper-wrapper.thumbnail_list_vertical {

			}
		}

		&.view_type_list {

		}
	}
}