@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

$btnProps: (
	'border-radius': 6px,
	'border-radius-sm': 4px,
	'height-lg': 56px,
	'height-md': 50px,
	'height-sm': 44px,
	'height-xs': 36px,
	'height-xxs': 30px,
	'height-min': 24px,
	'height-ip': 50px,
	'padding': 0 23px 1px,
	'padding-sm': 0 23px 1px,
	'padding-xs': 0 13px 1px,
	'padding-xxs': 0 9px 1px,
	'padding-min': 0 5px 1px,
	'padding-ip': 0 23px 1px,
	'min-width-lg': 165px,
	'min-width-md': 165px,
	'min-width-sm': 86px,
);

/* 버튼 */
button[class^=btn_], a[class^=btn_] {
	display: inline-block;
	box-sizing: border-box;
	vertical-align: top;

	// 이벤트/비활성화
	&:hover {
		cursor: pointer;
	}
	&[disabled], &[aria-disabled], &.disabled {
		pointer-events: none;
		cursor: default;
	}

	// 아이콘
	[class^=ico_] {
		display: inline-block;
		position: relative;
		vertical-align: top;
	}

	// 텍스트
	.text {
		display: inline-block;
		position: relative;
		vertical-align: top;
	}
}

/* 버튼 - 사이즈 */
a.btn_lg, button.btn_lg {
	@include mixins.justify_width(center, center, row, true);
	min-width: map.get($btnProps, 'min-width-lg');
	height: map.get($btnProps, 'height-lg');
	padding: map.get($btnProps, 'padding');
	border-radius: map.get($btnProps, 'border-radius');
	@include mixins.font_size_md;
	font-family: variables.$fontKor;
	font-weight: variables.$fontWeightBold;

	&.btn_pill {
		border-radius: map.get($btnProps, 'height-lg');
	}
}
a.btn_md, button.btn_md {
	@include mixins.justify_width(center, center, row, true);
	min-width: map.get($btnProps, 'min-width-md');
	height: map.get($btnProps, 'height-md');
	padding: map.get($btnProps, 'padding');
	border-radius: map.get($btnProps, 'border-radius');
	@include mixins.font_size_def;
	font-family: variables.$fontKor;
	font-weight: variables.$fontWeightBold;

	&.btn_pill {
		border-radius: map.get($btnProps, 'height-md');
