}
a.btn_sm, button.btn_sm {
	@include mixins.justify_width(center, center, row, true);
	height: map.get($btnProps, 'height-sm');
	padding: map.get($btnProps, 'padding-sm');
	border-radius: map.get($btnProps, 'border-radius');
	@include mixins.font_size_sm;
	font-family: variables.$fontKor;
	font-weight: variables.$fontWeightMedium;

	&.btn_pill {
		border-radius: map.get($btnProps, 'height-sm');
	}

	// 아이콘 : 최대 height 20px;
	[class^=ico_] {
		height: 20px;
		margin-right: 5px;

		&:last-child {
			margin-right: 0;
			margin-left: 5px;
		}
