/* 버튼 - 색상 */
a.btn_primary, button.btn_primary {
	color: #fff;
	background: variables.$kyoboPurple;
	border: 1px solid variables.$kyoboPurple;
	@include mixins.transition(background-color 0.2s ease-out, border-color 0.2s ease-out);

	&:focus, &:active {
		background: #2c307c;
		border-color: #2c307c;
	}
	&[disabled], &[aria-disabled], &.disabled {
		background: #a6a9d7;
		border-color: #a6a9d7;
	}
}
a.btn_light_gray, button.btn_light_gray {
	color: #fff;
	background: variables.$textColorLightGray;
	border: 1px solid variables.$textColorLightGray;
	@include mixins.transition(background-color 0.2s ease-out, border-color 0.2s ease-out);

	&:focus, &:active {
		background: variables.$textColorGray;
		border-color: variables.$textColorGray;
	}
	&[disabled], &[aria-disabled], &.disabled {
		background: #bebebe;
		border-color: #bebebe;
	}
}
a.btn_line_primary, button.btn_line_primary {
	color: variables.$textColorPurple;
	border: 1px solid variables.$textColorPurple;
	background: #fff;
	@include mixins.transition(background-color 0.2s ease-out, border-color 0.2s ease-out);

	&:focus, &:active {
		background: #efeff8;
		border-color: variables.$textColorPurple;
	}
	&[disabled], &[aria-disabled], &.disabled {
		color: rgba(variables.$textColorPurple, .5);
		background: #fff;
		border-color: #a6a9d7;

		[class^=ico_] {
			opacity: 0.4;
		}
	}
}
a.btn_line_gray, button.btn_line_gray {
	background: #fff;
	border: 1px solid variables.$colorGray02;
	@include mixins.transition(background-color 0.2s ease-out, border-color 0.2s ease-out);

	&:focus, &:active {
		background: variables.$colorGray05;
		border-color: variables.$colorGray02;
	}
	&[disabled], &[aria-disabled], &.disabled {
		color: variables.$colorGray01;
		border-color: variables.$colorGray04;

		[class^=ico_] {
			opacity: 0.4;
		}
	}
}
a.btn_line_green, button.btn_line_green {
	color: variables.$kyoboGreen;
	background: #fff;
	border: 1px solid variables.$kyoboGreen;
	@include mixins.transition(background-color 0.2s ease-out);

	&:focus, &:active {
		background: #f3ffef;
	}

	&[disabled], &[aria-disabled], &.disabled {
		[class^=ico_] {
			opacity: 0.4;
		}
	}
}

/* 알림 on/off 버튼 */
a.btn_alarm, button.btn_alarm {
	color: variables.$textColorLightGray;
	font-weight: variables.$fontWeightMedium;
	border: 1px solid variables.$colorGray02;

	.ico_alarm {
		display: inline-block;
		width: 14px;
		height: 18px;
		font-size: 0;
		background: {
			position: left 3px;
			repeat: no-repeat;
		}
		@include mixins.ratio_images('../images/ico_alarm_off_mok', 'png', 14px, 14px);
	}

	.text {
		position: relative;
		padding-right: 1px;
	}

	&.active {
		color: variables.$kyoboPurple;
		border-color: variables.$kyoboPurple;

		.ico_alarm {
			background: {
				position: 1px center;
			}
			@include mixins.ratio_images('../images/ico_alarm_on_mok', 'png', 12px, 18px);
		}
	}
}

/* 버튼 - 텍스트 링크 */
a.btn_text_link, button.btn_text_link {
	color: variables.$textColorLightGray;
	@include mixins.font_size_xs;
	font-weight: variables.$fontWeightRegular;
	text-align: left;

	&.row_2_ellipsis {
		.text {
			@include mixins.multi_ellipsis(variables.$fontSizeXsLineHeight, 2, false);
		}
	}

	&.size_lg {
		@include mixins.font_size_sm;
		font-weight: variables.$fontWeightMedium;

		&.row_2_ellipsis {
			.text {
				@include mixins.multi_ellipsis(variables.$fontSizeSmLineHeight, 2, false);
			}
		}
	}

	[class^=ico_] {
		margin-left: 4px;
	}

    &:focus, &:active{
        .text {
            text-decoration: underline;
        }
    }
}

/* 버튼 - 더보기 */
// 본문 펼쳐보기
.btn_more_body {
	color: variables.$textColorLightGray;
	font-size: variables.$fontSizeSm;
	line-height: 20px;
	letter-spacing: variables.$letterSpacing;
	text-align: left;

	// 아이콘
	[class^=ico_]{
		height: 20px;
		margin-left: 4px;
	}
	.ico_arw {
		width: 20px;
		height: 20px;
		@include mixins.ratio_images('../images/btn_more_body_mok', 'png', 20px, 20px);
		background: {
			position: center;
			repeat: no-repeat;
		}
		@include mixins.transition(background 0.2s ease-out);
	}

	&:focus, &:active {
		.text {
			text-decoration: underline;
		}
	}

	&.active {
		.ico_arw {
			@include mixins.ratio_images('../images/btn_more_body_active_mok', 'png', 20px, 20px);
			background: {
				position: center;
				repeat: no-repeat;
			}
		}
	}
}

// 컨텐츠 하단 더보기
.btn_more_cont {
