	.ico_question {
		@include mixins.ratio_images('../images/btn_tooltip_question_sm_mok', 'png', 16px, 16px);
	}

	&.active {
		.ico_info {
			@include mixins.ratio_images('../images/btn_tooltip_sm_active_mok', 'png', 16px, 16px);
		}
	}
}
