/* 버튼 정렬 */
.btn_wrap {
	@include mixins.justify_width(start, center);
	text-align: center;

	&:not(.auto) {
		[class^=btn_] {
			flex: 1;
			min-width: auto;

			& + [class^=btn_] {
				margin-left: 5px;
			}
		}
	}

	&.title_bottom {
		margin-bottom: 20px;
	}

	&.page_bottom {
		margin-top: 50px;
	}

	&.auto{
		display: flex;

		[class^=btn_] {
			flex: 0 1 auto;

			& + [class^=btn_] {
				margin-left: 5px;
			}
		}
	}

	&.full{
		flex-wrap: wrap;

		[class^=btn_] {
			flex: 0 auto;
			width: 100%;
			min-width: auto;

			& + [class^=btn_] {
				margin: 8px 0 0;
			}
		}
	}

	&.overlap {
		.btn_lg, .btn_xs, .btn_ip {
			&:not(:only-child) {
				border-radius: 0;

				&:first-child {
					border-radius: 6px 0 0 6px;
				}

				&:last-child {
					border-radius: 0 6px 6px 0;
				}
			}
		}

		.btn_lg {
			+ .btn_lg {
				margin-left: -1px;
			}
		}
		.btn_xs {
			+ .btn_xs {
				margin-left: -1px;
			}
		}
		.btn_ip {
			+ .btn_ip {
				margin-left: -1px;
			}
		}

		&.single {
			border-radius: 6px;
		}
	}
}

.ico_trash {
	width: 15px;
	height: 18px;
	margin-right: 5px;
	background: {
		position: center;
		repeat: no-repeat;
	}
	@include mixins.ratio_images('../images/ico_trash_mok', 'png', 15px, 18px);
}