@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

/* 불릿 아이템 */
[class^='bul_item_'] {
	position: relative;
	@include mixins.font_size_sm;
	color: variables.$textColorGray;
	font-weight: variables.$fontWeightRegular;

	& + [class^='bul_item_'] {
		margin-top: 10px;
	}

	.bul_list {
		margin-top: 10px;
	}

	.btn_text_link {
		display: inline;
		color: inherit;
		font-size: inherit;
		line-height: inherit;

		.text {
			display: inline;
            text-decoration: underline;
		}
	}

	// font-size: 13px
	&.font_size_xs {
		@include mixins.font_size_xs;

		& + .font_size_xs {
			margin-top: 6px;
		}

		/*TODO : 디자인 수급 후 수정*/
		.bul_list {
			margin-top: 6px;
		}
	}

	// font-size: 12px
	&.font_size_xxs {
		padding-left: 6px;
		@include mixins.font_size_xxs;

		&:before {
			width: 2px;
			height: 2px;
		}

		& + .font_size_xxs {
			margin-top: 4px;
		}

		.bul_list {
			margin-top: 4px;
		}
	}
}

// 불릿 dot 타입
.bul_item_dot {
	padding-left: 9px;

	&:before {
		content:'';
		display: block;
		position: absolute;
		left: 0;
		top: 7px;
		width: 3px;
		height: 3px;
		border-radius: 50%;
		background-color: variables.$textColorLightGray;
	}
}

// 불릿 asterisk 타입
.bul_item_asterisk{
	color: variables.$textColorLightGray;
	padding-left: 8px;

	&:before{
		content:'*';
		color: variables.$textColorPurple;
		position: absolute;
		left: 0;
	}
}

// 불릿 square 타입
.bul_item_square {
	padding-left: 8px;
	&:before {
		content:'';
		display: block;
		position: absolute;
		left: 0;
		top: 8px;
		width: 2px;
		height: 2px;
		background-color: rgba(variables.$textColorLightGray, .5);
	}
}

// 불릿 branch 타입
.bul_item_branch {
	padding-left: 11px;
	&:before {
		content:'';
		display: block;
		position: absolute;
		left: 0;
		top: 5px;
		width: 6px;
		height: 6px;
		background: {
			position: center;
			repeat: no-repeat;
		}
		@include mixins.ratio_images('../images/bul_branch_mok', 'png', 6px, 6px);
	}
}

/* decimal 리스트 */
.decimal_item {
	// font-size: 14px;
	color: variables.$textColorGray;
	@include mixins.font_size_sm;
	font-weight: variables.$fontWeightRegular;

	.btn_text_link {
		color: inherit;
		font-size: inherit;
		line-height: inherit;

		.text {
			display: inline;
            text-decoration: underline;
		}
	}

	// font-size: 13px
	&.font_size_xs {
		@include mixins.font_size_xs;
	}

	// font-size: 12px
	&.font_size_xxs {
		@include mixins.font_size_xxs;
	}
}
.decimal_list {
	padding-left: 15px;
	list-style: decimal;
	box-sizing: border-box;
}

/* 텍스트 gray 박스 */
.info_text_box {
	display: block;
	padding: 18px;
	border-radius: 10px;
	background-color: variables.$colorGray06;
	box-sizing: border-box;

	.info_text_inner {
		+ .info_text_inner{
			margin-top: 18px;
			padding-top: 18px;
			border-top: 1px solid variables.$colorGray04;
		}
	}

	&.size_sm {
		padding: 14px;

		[class^=bul_item_].font_size_min {

			& + [class^=bul_item_].font_size_min {
				margin-top: 4px;
			}
		}
	}
}

/* 뷸렛 리스트 아래 타이틀이 있을 때 간격 */
.bul_list {
	& + .title_wrap {
		&.title_size_sm {
			margin-top: 20px;
		}
		&.title_size_xs{
			margin-top: 18px;
		}
		&.title_size_min{
			margin-top: 16px;
		}
	}
}

/* 타이틀과 불릿리스트와의 간격 */
.title_wrap {
	&.title_size_sm + .bul_list {
		margin-top: 12px;
	}
	&.title_size_xs + .bul_list {
		margin-top: 10px;
	}
	&.title_size_min + .bul_list {
		margin-top: 8px;
	}
}

.title_wrap.title_size_sm + [class*='bul_list'] {
	margin-top: 12px;
}

.title_wrap.title_size_xs + [class*='bul_list'] {
	margin-top: 10px;
}

.title_wrap.title_size_min + [class*='bul_list'] {
	margin-top: 8px;
}

/* 단순 정보성 텍스트 */
.info_text {
	@include mixins.font_size_sm;
	color: variables.$textColorGray;

	.btn_text_link {
		display: inline;
		color: inherit;
		font-size: inherit;
		line-height: inherit;

		.text {
			display: inline;
            text-decoration: underline;
		}
	}

	&.font_size_xs{
		@include mixins.font_size_xs;

		+ .info_text{
			&.font_size_xs{
				/* TODO : 가이드에 13PX + 13PX 사이 여백 추가될 때 수정 필요 */
				margin-top: 16px;
			}
		}
	}

	&.font_size_xxs{
		@include mixins.font_size_xxs;

		+ .info_text{
			&.font_size_xxs{
				/* TODO : 가이드에 12PX + 12PX 사이 여백 추가될 때 수정 필요 */
				margin-top: 16px;
			}
		}
	}

	&.font_size_def{
		@include mixins.font_size_def;
		color: variables.$textColorBlack;
		font-weight: variables.$fontWeightMedium;
		margin-bottom: 13px;
	}

	+ .info_text{
		margin-top: 14px;
	}
}


/* STEP 리스트 */
.ord_progress_list {
	position: relative;
	font-size: 0;

	.ord_progress_item {
		@include mixins.justify_width(center);
		position: relative;
		z-index: 1;

		[class^=ico_] {
			flex: 0 auto;
			width: 58px;
		}

		.ord_progress_step {
			display: inline-block;
			flex: 1;
			margin-left: 12px;
			vertical-align: top;

			.ord_step_num {
				display: block;
				@include mixins.font_size_xs;
				font-weight: variables.$fontWeightBold;
				color: variables.$textColorPurple;
				vertical-align: top;
			}

			.ord_step_text {
				display: block;
				@include mixins.font_size_sm;
				vertical-align: top;
				margin-top: 3px;
			}
		}

		& + .ord_progress_item {
			padding-top: 16px;
		}

		.btn_text_link {
			display: inline;
			color: inherit;
			font-size: inherit;
			line-height: inherit;

			.text {
				display: inline;
				text-decoration: underline;
			}
		}
	}

	&:before {
		content: '';
		position: absolute;
		top: 29px;
		left: 29px;
		width: 1px;
		height: calc(100% - 58px);
		@include mixins.ratio_images('../images/line_dot_vertical_mok', 'png', 1px, 3px);
		background: {
			repeat: repeat-y;
		};
	}
}