@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

/* 타이틀 영역 */
.title_wrap {
	position: relative;
	@include mixins.justify_width(center);

	// 타이틀 텍스트 부분 사이즈별
	.title_heading {
		display: inline-block;
		font-weight: variables.$fontWeightBold;

		.title_prefix {
			display: inline-block;
			color: variables.$textColorGray;
			margin-right: 3px;
			font-weight: variables.$fontWeightRegular;
		}
	}

	// font-size: 20px
	&.title_size_xl {
		margin-bottom: 20px;

		.title_heading {
			@include mixins.font_size_xl;
		}

		&.align_both{
			min-height: 36px;
			margin-bottom: 16px;
		}

		&.has_sub_title{
			margin-bottom: 24px;
		}
	}

	// font-size: 16px
	&.title_size_md {
		margin-bottom: 18px;

		.title_heading {
			@include mixins.font_size_md;
		}

		&.align_both{
			min-height: 30px;
			margin-bottom: 14px;
		}

		&.has_sub_title{
			margin-bottom: 20px;
		}
	}

	// font-size: 15px
	&.title_size_def {
		margin-bottom: 12px;

		.title_heading {
			@include mixins.font_size_def;
		}
	}

	// font-size: 14px
	&.title_size_sm {
		margin-bottom: 10px;

		.title_heading {
			@include mixins.font_size_sm;

			.ico_speech{
				margin: 2px 4px 0 0;
			}
		}
	}

	// font-size: 13px
	&.title_size_xs {
		margin-bottom: 8px;

		.title_heading {
			@include mixins.font_size_xs;
		}
	}


	&.has_sub_title{
		flex-wrap: wrap;

		.title_heading{
			display: block;
			margin-bottom: 0;
		}

		.info_text, [class^='bul_item_']{
			width: 100%;
			margin-top: 6px;
		}
	}

	&.has_btn{
		// 타이틀 영역 왼쪽/오른쪽 영역
		.right_area {
			margin-left: auto;
			flex-shrink: 0;
			@include mixins.justify_width(center, start, row);

			// 기본 간격 조절
			[class^='btn_']:not(.btn_xs) {
				~ [class^='btn_']:not(.btn_xs){
					margin-left: 6px;
				}
			}

			.btn_xs {
				~ .btn_xs{
					margin-left: 3px;
				}
			}

			.form_chk{
				+ [class^='btn_']{
					margin-left: 20px;
				}
			}
		}
	}

	&.type_sub_title{
		margin-bottom: 20px;

		.title_heading{
			font-weight: variables.$fontWeightMedium;
		}
	}

	.count {
		display: inline-block;
		min-width: 20px;
		padding: 0 6px;
		margin: -3px 0 0 5px;
		color: #fff;
		@include mixins.font_size_xxs;
		line-height: 20px;
		font-weight: variables.$fontWeightBold;
		background: #000;
		border-radius: 20px;
		text-align: center;
		box-sizing: border-box;
		vertical-align: top;
	}
}

/* 컬러 */
.fc_spot{
	color: variables.$textColorPurple !important;
}
.fc_black{
	color: variables.$textColorBlack !important;
}
.fc_green {
	color: variables.$textColorGreen !important;
}

/* 폰트 굵기 */
.fw_bold {
	font-weight: variables.$fontWeightBold !important;
}
.fw_medium {
	font-weight: variables.$fontWeightMedium !important;
}
.fw_regular {
	font-weight: variables.$fontWeightRegular !important;
}
