@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

/* Table Row Wrap */
.tbl_row_wrap{
	border-top: 1px solid variables.$textColorBlack;
	border-bottom: 1px solid variables.$colorGray04;

	.tbl_row{
		width: 100%;

		tr{
			th{
				padding: 7px 20px 7px 4px;
				@include mixins.font_size_sm;
				font-weight: variables.$fontWeightMedium;
				text-align: left;
				vertical-align: top;
			}

			td{
				padding: 7px 4px 7px 0;
				@include mixins.font_size_sm;
				vertical-align: top;
				color: variables.$textColorGray;

				.btn_text_link{
					color: inherit;
					.text{
						text-decoration: underline;
					}
				}
			}

			&:first-child{
				th, td{
					padding-top: 20px;
				}
			}

			&:last-child{
				th, td{
					padding-bottom: 20px;
				}
			}
		}

		+ .tbl_row{
			border-top: 1px solid variables.$colorGray04;
		}
	}

	.tbl_header_info{
		padding: 20px 0;
		border-bottom: 1px solid variables.$colorGray04;
	}
}

/* Table Column Wrap */
.tbl_col_wrap {
	display: block;
	width: 100%;
	border-top: 1px solid variables.$textColorBlack;
	border-bottom: 1px solid variables.$colorGray04;
	box-sizing: border-box;

	/* Table Column Type */
	.tbl_col {
		width: 100%;

		tr{
			th {
				padding: 14px 10px 13px; // 여백 팝업기준으로 작업. 바닥페이지에서 tbl_col 여백 다른경우 팝업과 분기처리 필요
				text-align: center;
				@include mixins.font_size_xs;
				color: variables.$textColorLightGray;
				font-weight: variables.$fontWeightMedium;
				vertical-align: middle;
			}

			td {
				padding: 10px 10px; // 여백 팝업기준으로 작업. 바닥페이지에서 tbl_col 여백 다른경우 팝업과 분기처리 필요
				text-align: center;
				@include mixins.font_size_xs;
				font-weight: variables.$fontWeightRegular;
				vertical-align: middle;

				.btn_text_link{
					color: inherit;
					.text{
						text-decoration: underline;
					}
				}

				&.align_left {
					text-align: left;
				}

				&.align_right {
					text-align: right;
				}
			}
		}
	}

	&:not(.type_th_gray) {
		.tbl_col {
			tbody {
				tr {
					&:first-child {
						border-top: 1px solid variables.$colorGray03;

						td {
							padding-top: 16px;
						}
					}

					&:last-child {
						td {
							padding-bottom: 20px;
						}
					}
				}
			}
		}
	}

	&.type_th_gray {
		.tbl_col {
			tr {
				th {
					background-color: variables.$colorGray06;
					border-bottom: 1px solid variables.$colorGray03;
				}

				td {
					padding: 16px 10px;
					border-top: 1px solid variables.$colorGray04; // TODO : 임의작업. 추후 케이스 나오면 케이스 맞춰 수정
				}

				~ tr{
					th{
						border-top: 1px solid variables.$colorGray04;
					}
				}
			}
		}
	}
}